import React, {Component} from 'react';
import {View, FlatList, StyleSheet, Dimensions, SafeAreaView, RefreshControl, Alert} from 'react-native';
import { Overlay } from 'react-native-elements';
import Modal from 'modal-react-native-web';

import {LocationCard} from '../components/LocationCard';
import { ButtonTab } from '../components/ButtonTab';
import { ElementFilterPlate } from '../components/ElementFilterPlate';

import {graphqlAuth} from '../graphql/graphqlService';
import {getUserAuth} from '../graphql/sessionData';
import {createSearchBody} from '../graphql/queries';
import * as Permissions from 'expo-permissions';

import {Linking} from 'expo';

const { height, width } = Dimensions.get('window');
const cardSize = Math.min(width, height) * .9;
let mainBackgroundColor = "#F8F7F0";

export class SearchLocationsArea extends Component {

  state = {
    data: [],
    start: 0,
    max: 10,
    distance: 50,
    isRefreshing: false,
    reachedEnd: false,
    filters: null,
    fuzzyName: null,
    showFilterPlate: false
  };

  constructor (props) {
    super(props);
  }

  componentDidMount() {
    Linking.getInitialURL().then((data) => {
      let { path, queryParams } = Linking.parse(data);
      if(path.includes('ViewLocation') && queryParams.locationId !== undefined){ 
          this._viewLocation(queryParams.locationId);
      }
    });    
    this._getUserPermissions()
      .then((result) => {
        if (result.status === 'granted'){
          this._getUserLocation();
        } else {
          Alert.alert(result);
        }
      });
  }

  async _getUserPermissions(){
    return await Permissions.askAsync(Permissions.LOCATION)
  }

  _getUserLocation() {
    navigator.geolocation.getCurrentPosition(
			position => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        });
        this._fetchLocations();
			},
			error => Alert.alert(error.message),
			{ enableHighAccuracy: true, timeout: 20000, maximumAge: 1000 }
		);
  }

  _viewLocation = (locationId) => {
    this.props.navigation.navigate('ViewLocation', {locationId: locationId})
  };

  _viewMap = () => {
    this.props.navigation.navigate('SearchLocationsMap', {latitude: this.state.latitude, longitude: this.state.longitude})
  };

  _setFuzzyName = (text) => {
    this.setState({fuzzyName: text});
  };

  _setFilters = (filters) => {
    this.setState({filters: filters});  
  };

  _toggleShowFilterPlate = () => {
    this.setState({showFilterPlate: !this.state.showFilterPlate});
  };

  _fetchLocations = () => {
    if(this.state.reachedEnd){
      return;
    }
    let body = createSearchBody(this.state.start, this.state.max, this.state.fuzzyName, this.state.filters, this.state.latitude, this.state.longitude, this.state.distance);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
      (result) => {
        if(result.data === null){
          return;
        }
        let locationsModel = result.data.getLocationNearByElastic;
        let paging = locationsModel.paging;
        let locations = locationsModel.locations;
        if(paging.stop - paging.start !== this.state.max){
          this.setState({reachedEnd: true});      
        }
        this.setState({
          data: [...this.state.data, ...locations],
          isRefreshing: false
        });
      },
      (error) => {
        this.setState({isRefreshing : false});
      }
    );
    }
    );
  };

  _refreshLocations = () => {
    this.setState({
      reachedEnd: false,
      isRefreshing: true,
      data: [],
      start: 0
    },() => {
      this._fetchLocations();
    });
  }

  _handleLoadMore = () => {
    this.setState(
      (prevState, nextProps) => ({
        start: prevState.start + this.state.max,
      }),
      () => {
        this._fetchLocations();
      }
    );
  };

  render() {
    return (
      <SafeAreaView style={{    
        flex: 1,
        flexDirection: 'column',
        height: height,
        width: width,
        backgroundColor: mainBackgroundColor,
        }}>    
        <FlatList
          data={this.state.data}
          removeClippedSubviews={true}
          keyExtractor={item => item.id}
          ListFooterComponent={ 
            <View style={styles.listFooter} />
          }
          getItemLayout={(data, index) => (
            {length: cardSize, offset: cardSize * index, index}
          )}
          refreshControl={
            <RefreshControl
              refreshing={this.state.isRefreshing}
              onRefresh={this._refreshLocations.bind(this)}
            />
          }
          renderItem={({item}) => 
            <LocationCard
              location={item}
              onPress={() => this._viewLocation(item.id)}/>
          }
          onEndReached={this._handleLoadMore}
          onEndReachedThreshold={50}
          initialNumToRender={5}
          windowSize={15}
        />
        
        <View style={styles.containerButtonFloatingLeft}>
        <ButtonTab
          iconNameAdd={'magnify'}
          iconName={'filter-variant'}
          buttonBackgroundColor={'#FFFFFF'}
          hasShadow={true}
          leftTab={true}
          onPress={() => this._toggleShowFilterPlate()}
        />
        <View style={{height:6}} /> 
        <ButtonTab
          iconName={'refresh'}
          buttonBackgroundColor={'#FFFFFF'}
          hasShadow={true}
          leftTab={true}
          onPress={() => {this._refreshLocations()}}
        />
        <View style={{height:6}} />
        <ButtonTab
          iconName={'map-search'}
          buttonBackgroundColor={'#FFFFFF'}
          hasShadow={true}
          leftTab={true}
          onPress={() => {this._viewMap()}} 
        />
        </View>
        <Overlay 
        ModalComponent={Modal} 
        isVisible={this.state.showFilterPlate}
        onBackdropPress={this._toggleShowFilterPlate}
        backdropStyle={{backgroundColor:"rgba(0, 0, 0, 0.7)", borderWidth:0}}
        overlayStyle={{width: (width>1000) ? '40%' : '85%', backgroundColor:'rgba(0, 0, 0, 0.0)', borderWidth:0, shadowColor:'rgba(0, 0, 0, 0.0)', shadowOpacity:0, shadowRadius:0, elevation:0}}>
          <ElementFilterPlate
            onPress={() => {this._refreshLocations(); this._toggleShowFilterPlate();}}  
            onClose={() => {this.setState({showFilterPlate: false});}}
            onChange={(filters) => this._setFilters(filters)}
            onChangeText={(text) => this._setFuzzyName(text)}
            />
        </Overlay>
      </SafeAreaView>
    );
  };
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'column',
    height: height,
    width: width,
    backgroundColor: '#F8F7F0',
  },
  containerButtonFloatingLeft: {
    position: 'absolute',
    top: 20,
    left: 0
  },
  listHeader: {
    height: 43
  },
  listFooter: {
    height: 72
  },
});