import React from 'react';
import {View, Text} from 'react-native';
import {FormTextInput} from '../components/FormTextInput';
import {ButtonPrimary} from '../components/ButtonPrimary';
import {getUserAuth} from '../graphql/sessionData';
import {graphqlAuth} from '../graphql/graphqlService';
import {createReportLocationBody, createReportReviewBody} from '../graphql/mutations';
import style from '../styles/Container.Form.Style';
import {notEmptyCS} from '../helpers/Constraints';
import { ScrollView } from 'react-native-gesture-handler';
import {ButtonTab} from '../components/ButtonTab';

function reportLocation(navigation, locationId, reason){
    let body = createReportLocationBody(locationId, reason);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
        (result) => {
            if(result.data !== null){
                navigation.goBack();
            }
        },
        (error) => {
            //do nothing
        }
    );});
};

function reportReview(navigation, locationId, reviewId, reason){
    let body = createReportReviewBody(locationId, reviewId, reason);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
        (result) => {
            if(result.data !== null){
                navigation.goBack();
            }
        },
        (error) => {
            //do nothing
        }
    );});
};

export function ReportLocationArea ({navigation, route}){
    const {locationId} = route.params;
    const [reason, onChangeReason] = React.useState('reason');
    return (
        <View style={style.containerBody}>            
            <ButtonTab
            styleContainer={{position:'absolute', top: 20, left: 0}}
            iconName={"arrow-left-bold"}
            buttonBackgroundColor={'#FFF'}
            hasShadow={true}
            leftTab={true}
            onPress={() => navigation.goBack()}/>
            <View style={style.containerMain}>
                <View style={style.containerTextInputLabels}>
                    <Text style={style.textLabel}>Reason</Text>
                </View>  
                <FormTextInput 
                    value={name}
                    onChangeText={reason => onChangeReason(reason)}
                    constraints={notEmptyCS}/>
                <ButtonPrimary 
                    text="Report Location"
                    onPress={() => reportLocation(navigation, locationId, reason)}/>
            </View>        
        </View>
    );
};

export function ReportReviewArea ({navigation, route}){
    const {locationId, reviewId} = route.params;
    const [reason, onChangeReason] = React.useState('reason');
    return (
        <View style={style.containerBody}>
            <ButtonTab
            styleContainer={{position:'absolute', top: 20, left: 0}}
            iconName={"arrow-left-bold"}
            buttonBackgroundColor={'#FFF'}
            hasShadow={true}
            leftTab={true}
            onPress={() => navigation.goBack()}/>
            <View style={style.containerMain}>
                <View style={style.containerTextInputLabels}>
                    <Text style={style.textLabel}>Reason</Text>
                </View>  
                <FormTextInput 
                    value={name}
                    onChangeText={reason => onChangeReason(reason)}
                    constraints={notEmptyCS}/>
                <ButtonPrimary 
                    text="Report Review"
                    onPress={() => reportReview(navigation, locationId, reviewId, reason)}/>
            </View>        
        </View>
    );
};