import * as React from 'react';
import 'react-native-gesture-handler';
import {useLinking, NavigationContainer} from '@react-navigation/native';

import {LoginNavigator} from './src/navigators/LoginStackNavigator';
import {HomeNavigator} from './src/navigators/HomeStackNavigator';

import {LoadingIndicator} from './src/components/LoadingIndicator';

import {graphqlAuth} from './src/graphql/graphqlService';
import {createVerifyAuthenticationBody} from './src/graphql/queries';

import {getUserAuth, removeUserAuth} from './src/graphql/sessionData';
import {AuthContext} from './src/context/Authentication';

import {Linking} from 'expo';

const prefix = Linking.makeUrl('/');

export default function App() {  
  const ref = React.useRef();
  const { getInitialState } = useLinking(ref, {
    prefixes: [prefix]
  });

  const [isReady, setIsReady] = React.useState(false);
  const [initialState, setInitialState] = React.useState();

  const [state, dispatch] = React.useReducer(
    (prevState, action) => {
      switch (action.type) {
        case 'LOGIN':
          return {
            userToken: action.token,
            isLoading: false,
          };
        case 'VERIFY_ATTEMPTED':
          return {
            ... prevState,
            isLoading: false,
          };
        case 'LOGOUT':
          return {
            userToken: null,
            isLoading: false,
          };
      }
    },
    {
      userToken: null,
      isLoading: true,
    }
  );

  React.useEffect(() => {
    getUserAuth()
    .then(token => {
      if(token !== null) {
        let body = createVerifyAuthenticationBody();
        graphqlAuth(token, body).then(
          (result) => {
            if(result.data !== null){
              dispatch({ type: 'LOGIN', token: token });
            } else {
              dispatch({ type: 'VERIFY_ATTEMPTED'})
            }
          },
          () => {
            dispatch({ type: 'VERIFY_ATTEMPTED'})
          }
        );
      } else {
        dispatch({ type: 'VERIFY_ATTEMPTED'});
      }    
    });
  }, [getUserAuth]);
  
  const authContext = React.useMemo(() => ({
      login: () => {
        getUserAuth()
          .then(token => {
            if(token !== null) {
              dispatch({ type: 'LOGIN', token: token });
            }    
          });
      },
      logout: () => {
        removeUserAuth()
          .then(() => {dispatch({ type: 'LOGOUT' });});
      }}),
    []
  );

  React.useEffect(() => {
    getInitialState()
      .catch(() => {})
      .then(result => {
        if (result !== undefined) {
          //do nothing
        }
        setIsReady(true);
      });
  }, [getInitialState]);

  if(state.isLoading === true || !isReady){
    return <LoadingIndicator/>
  }

  return(
    <AuthContext.Provider value={authContext}>
      {
        (<NavigationContainer linking={initialState} ref={ref} fallback={<LoadingIndicator/>}>    
          {state.userToken == null ? (<LoginNavigator/>) : (<HomeNavigator/>)}
        </NavigationContainer>)
      }
    </AuthContext.Provider>
  );
}
