import React from 'react';
import {ScrollView, View, Alert, StyleSheet, Dimensions} from 'react-native';

import {FormTextInput} from '../components/FormTextInput';
import {ButtonPrimary} from '../components/ButtonPrimary';
import {default as TextTitle} from '../components/TextTitle';
import {default as TextBody} from '../components/TextBody';
import {AddReviewImageArea} from './AddReviewImage'
import {RatingButtonGroup} from '../components/RatingButtonGroup';
import {ButtonTab} from '../components/ButtonTab';
import {Dialog} from '../components/Dialog';

import {getUserAuth} from '../graphql/sessionData';
import {graphqlAuth,imageAuth} from '../graphql/graphqlService';
import {createAddReviewBody} from '../graphql/mutations';
import {reviewTitleCS, reviewBodyCS} from '../helpers/Constraints';

import {wifiIcons, wifiValues, noiseIcons, noiseValues, seatingIcons, seatingValues, outletsIcons, outletsValues} from '../icons/RatingIcons';
import style from '../styles/Container.Form.Style'; 


const { height, width } = Dimensions.get('window');
const trdWidth = (width/3);

let containerMainWidth;


function addImage(token, image, locationId, reviewId){
    if (image === null){
        return;
    }
    imageAuth(token, image, locationId, reviewId).then(
        (result) => {
            if(result.data === null){
                return;
            }
        },
        (error) => {
            Alert.alert(error);
        }
    );
};

function addReview(locationId, noise, wifi, seating, outlets, title, review, images, navigation){
    let body = createAddReviewBody(locationId, noise, wifi, seating, outlets, title, review);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
        (result) => {
            if(result.data === null){
                return;
            }
            images.forEach(image => {
                addImage(token, image, locationId, result.data.createNewReview.id);
            });
            navigation.goBack();
        },
        (error) => {
            Alert.alert(error);
        }
    );});
};

function titleFontSize(width){
    return width>1000 ? 42 : 30
};

function bodyFontSize(width){
    return width>1000 ? 18 : 14
};

export function AddReviewArea ({route, navigation}){
    const {locationId, locationName} = route.params;
    const [noise, onChangeNoise] = React.useState('noise');
    const [wifi, onChangeWifi] = React.useState('wifi');
    const [seating, onChangeSeating] = React.useState('seating');
    const [outlets, onChangeOutlets] = React.useState('outlets');
    const [title, onChangeTitle] = React.useState('title');
    const [review, onChangeReview] = React.useState('review');
    const [images, onChangeImages] = React.useState('images');
    const [confirmGoBack, setConfirmGoBack] = React.useState(false);

    return (
        <>
        <ScrollView style={styles.containerBody}>
            <View style={styles.containerMain}>
                <TextBody text={"Write your review for"} />
                <TextTitle text={locationName} weight={'bold'} fontSize={titleFontSize(width)} />
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Title'} weight={'semiBold'} />
                </View>  
                <FormTextInput 
                    value={title}
                    onChangeText={text => onChangeTitle(text)}
                    constraints={reviewTitleCS}/>
                <View style={style.containerTextInputLabels}>
                <TextBody text={"WIFI"} weight={'semiBold'} fontSize={bodyFontSize(width)} />
                </View>  
                <RatingButtonGroup 
                    icons={wifiIcons}
                    ratings={wifiValues}
                    containerWidth={'100%'}
                    onPress={onChangeWifi}/>
                <View style={style.containerTextInputLabels}>
                <TextBody text={"NOISE"} weight={'semiBold'} fontSize={bodyFontSize(width)} />
                </View>  
                <RatingButtonGroup 
                    icons={noiseIcons}
                    ratings={noiseValues}
                    containerWidth={'100%'}
                    onPress={onChangeNoise}/>
                <View style={style.containerTextInputLabels}>
                <TextBody text={"SEATING"} weight={'semiBold'} fontSize={bodyFontSize(width)} />
                </View>  
                <RatingButtonGroup 
                    icons={seatingIcons}
                    ratings={seatingValues}
                    containerWidth={'100%'}
                    onPress={onChangeSeating}/>
                <View style={style.containerTextInputLabels}>
                <TextBody text={"OUTLETS"} weight={'semiBold'} fontSize={bodyFontSize(width)} />
                </View>                     
                <RatingButtonGroup 
                    icons={outletsIcons}
                    ratings={outletsValues}
                    containerWidth={'100%'}
                    onPress={onChangeOutlets}/>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Review Body'} weight={'semiBold'} />
                </View>                  
                <FormTextInput 
                    isMultiline={true}
                    numberOfLines={6}
                    value={review}
                    onChangeText={text => onChangeReview(text)}
                    constraints={reviewBodyCS}/>
                <AddReviewImageArea
                onChange={onChangeImages}/>
                <ButtonPrimary 
                    text="Submit"
                    onPress={() => addReview(locationId, noise, wifi, seating, outlets, title, review, images, navigation)}/>
           </View>         
        </ScrollView>
        <ButtonTab
        styleContainer={{position:'absolute', top: 20, left: 0}}
        iconName={"arrow-left-bold"}
        buttonBackgroundColor={'#FFF'}
        hasShadow={true}
        leftTab={true}
        onPress={() => {
            if(!confirmGoBack){
                setConfirmGoBack(true)
            }
        }}/>
        {confirmGoBack &&
            <Dialog 
            title={"Dismiss this review?"} 
            text={"Going back will discard this review. Are you sure you want to dismiss this review and go back?"} 
            buttonConfirmText={"Go Back"} 
            buttonDenyText={"Cancel"}
            onConfirm={() => {navigation.goBack()}}
            onDeny={() => {setConfirmGoBack(false)}} 
            hasShadow={true}
            />
        }
        </>
    );
};

function choseStyle(width){
    if(width > 1000){
      containerMainWidth = trdWidth
    } else {
      containerMainWidth = width - 40
    }
  }
  
choseStyle(width);

const styles = StyleSheet.create({
    containerBody: {
        backgroundColor: '#F8F7F0',
        width: width,
        height: height,
        paddingBottom: 130
    },
    containerMain: {
        width: containerMainWidth,
        flexDirection: 'column',
        margin: 20,
        padding: 16,
        borderRadius: 4,
        alignSelf: 'center',
        alignItems: 'center',
        backgroundColor: '#fff',
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 3,
        },
        shadowOpacity: 0.29,
        shadowRadius: 4.65,
        elevation: 7,
      },
});