import {wifiFilterIcons, wifiFilterValues, noiseFilterIcons, noiseFilterValues, seatingFilterIcons, seatingFilterValues, outletsFilterIcons, outletsFilterValues, noRatingIcon} from '../icons/RatingIcons';

export function wifiRatingConverter(ratingValue) {
    switch (ratingValue) {
        case wifiFilterValues[4]:
            return wifiFilterIcons[4];
        case wifiFilterValues[3]:
            return wifiFilterIcons[3];
        case wifiFilterValues[2]:
            return wifiFilterIcons[2];
        case wifiFilterValues[1]:
            return wifiFilterIcons[1];
        case wifiFilterValues[0]:
            return wifiFilterIcons[0];
        default:
            return noRatingIcon[0];
    }
}

