export function distanceConverter(distanceRaw, numOfDec){
    return Number(Math.round(distanceRaw + 'e' + numOfDec) + 'e-' + numOfDec);
}

export function resizeLocationName(locationName, fontSizeLong, fontSizeShort){
    if(locationName.length > 20){
        return fontSizeLong;
    } else {
        return fontSizeShort;
    }
}
