import React from 'react';
import {View, StyleSheet, Image, TouchableOpacity, Dimensions} from 'react-native';

import {BarIcon} from "./BarIcon";
import { default as TextTitle} from './TextTitle';
import { default as TextBody} from './TextBody';
import {ButtonIcon} from './ButtonIcon';

import Carousel, { Pagination } from 'react-native-snap-carousel';
import { Overlay } from 'react-native-elements';
import Modal from 'modal-react-native-web';

import { wifiRatingConverter, noiseRatingCoverter, seatingRatingConverter, outletRatingConverter} from '../helpers/Converters';
import { setRatingIconColor } from '../helpers/SetRatingIconColor';

const { height, width } = Dimensions.get('window');
let imageSize;

function choseStyle(width){
    if(width > 1000){
      imageSize = width/2.5
    } else {
      imageSize = width;
    }
  }

choseStyle(width);

export class ReviewCard extends React.Component {
    state = {
      index: 0,
      imageIndexPressed: 0,
      activeSlide: 0,
      showReviewImages: false,
    };

    _renderItem = ({item, index}) => {
      return (
        <Image source={{ uri: item }} style={{ width: imageSize-20, height: imageSize-20, alignSelf: 'center', borderRadius: 4}} />    
      );
    }

    get pagination () {
      return (
        <Pagination
          dotsLength={this.props.review.imageUris.length}
          activeDotIndex={this.state.activeSlide}
          containerStyle={{}}
          dotStyle={{
              width: 10,
              height: 10,
              borderRadius: 5,
              marginHorizontal: 4,
              backgroundColor: 'rgba(255, 255, 255, 0.92)'
          }}
          inactiveDotStyle={{
              // Define styles for inactive dots here
          }}
          inactiveDotOpacity={0.4}
          inactiveDotScale={0.6}
        />
      );
    }


    _toggleShowReviewImages = (itemIndex) => {
      this.setState({imageIndexPressed: itemIndex});
      this.setState({activeSlide: itemIndex});
      this.setState({showReviewImages: !this.state.showReviewImages});
    };

    _createTopReviewHalf = (review) => {
      return(
        <>
        <TextTitle text={review.title} weight={'bold'} fontSize={18}/>
        <View style={styles.containerRatings}>
          <BarIcon 
          iconNames={[wifiRatingConverter(review.wifi),noiseRatingCoverter(review.noise),seatingRatingConverter(review.seating),outletRatingConverter(review.outlets)]}
          iconColors={[setRatingIconColor("E"),setRatingIconColor("E"),setRatingIconColor("E"),setRatingIconColor("E")]}
          labelColor= {'#CCC'}
          containerOrientation = {'row'}
          iconSize = {18} />
        </View>
        <View style={styles.divider} />
        </>
      );
    }

    _createImageSection = (review) => {
      return(
        <>
        <View style={styles.containerThumbnails}>
          <TouchableOpacity onPress={() => this._toggleShowReviewImages(0)}>
            <Image source={review.imageUris[0]} style={styles.thumbnail} />
          </TouchableOpacity>
          <TouchableOpacity onPress={() => this._toggleShowReviewImages(1)}>
            <Image source={review.imageUris[1]} style={styles.thumbnail} />
          </TouchableOpacity>
          <TouchableOpacity onPress={() => this._toggleShowReviewImages(2)}>
            <Image source={review.imageUris[2]} style={styles.thumbnail} />
          </TouchableOpacity>
        </View>
        <Overlay 
        ModalComponent={Modal} 
        isVisible={this.state.showReviewImages} 
        onBackdropPress={this._toggleShowReviewImages} 
        backdropStyle={{backgroundColor:"rgba(0, 0, 0, 0.7)", borderWidth:0}}
        overlayStyle={{backgroundColor:'rgba(0, 0, 0, 0.0)', borderWidth:0, shadowColor:'rgba(0, 0, 0, 0.0)', shadowOpacity:0, shadowRadius:0, elevation:0}}>
          <View style={{width:imageSize, flexDirection:'row-reverse', alignSelf: 'center',}}>
            <ButtonIcon
            iconName={'close'}
            iconColor={'#FFF'}
            onPress={() => {this.setState({showReviewImages: false});}} />
          </View>
          <Carousel
          layout='default'
          ref={(c) => { this._carousel = c; }}
          data={review.imageUris}
          renderItem={this._renderItem}
          sliderWidth={width}
          itemWidth={imageSize}
          firstItem={this.state.imageIndexPressed}
          onSnapToItem={ (index) => this.setState({activeSlide:index}) }
          enableSnap={true}
          enableMomentum={false}
          />
          { this.pagination }
        </Overlay>
        </>
      );
    }

    _createLowerReviewHalf = (review) => {
      const dateCreated = review.dateCreated.substring(0,10);
      return(
        <>
          <View style={styles.lowerRowContainer}>
            <View style={{flex: 1}}>
            <TextBody text={review.review} fontSize={16} />
            <TextBody text={"by "+ review.userId + " on " + dateCreated} fontSize={14} color={"#AAAAAA"}/>
            </View>
            { this.props.onPress && <View style={styles.moreInfoContainer}><ButtonIcon iconSize={24} iconName={'arrow-right-circle-outline'} onPress={() => this.props.onPress()}/></View>}
          </View>
        </>
      );
    }

    _createNewReview = (review, hasImages) => {
      const dateCreated = review.dateCreated.substring(0,10);
      return(
        <>
        <View style={[{...this.props.styleContainer, marginHorizontal:(this.props.margin != null) ? this.props.margin : 20},styles.containerMainAlt]}>
          {!hasImages &&
            <View style={{flex: 1, height:"100%", backgroundColor:"#9DB492", borderRadius:12}}/>
          }
          {hasImages &&
            <TouchableOpacity  style={{flex: 1, height:"100%"}} onPress={() => this._toggleShowReviewImages(0)}>
              <Image source={review.imageUris[0]} style={{height:'100%', width:'100%', borderRadius:12}} />
            </TouchableOpacity>
          }
          <View style={{flex: 2, height:"100%", padding:12}}> 
            <View style={{flex:2 ,}}>
              <TextBody text={review.title} weight={'bold'} fontSize={24}/>
              <TextBody style={{marginTop:4}} text={review.review} fontSize={14} />
            </View>
            <View style={{flex:1 ,flexDirection:'row', alignItems:'flex-end', justifyContent: 'space-between'}}>
              <View style={{flexDirection:'row'}}>
              <TextBody text={"by "+ review.userId} fontSize={14} weight={"semiBold"} color={"#222"}/>
              <TextBody text={" on " + dateCreated} fontSize={14} weight={"regular"} color={"#AAA"}/>
              </View>
              <View style={{padding:4, borderRadius:12, backgroundColor:"#f2f2f2"}}>
              <BarIcon 
              iconNames={[wifiRatingConverter(review.wifi),noiseRatingCoverter(review.noise),seatingRatingConverter(review.seating),outletRatingConverter(review.outlets)]}
              iconColors={[setRatingIconColor("E"),setRatingIconColor("E"),setRatingIconColor("E"),setRatingIconColor("E")]}
              labelColor= {'#CCC'}
              containerOrientation = {'row'}
              iconSize = {18} />
              </View>
            </View>
          </View>
        </View>

          {hasImages &&
          <Overlay 
          ModalComponent={Modal} 
          isVisible={this.state.showReviewImages} 
          onBackdropPress={this._toggleShowReviewImages} 
          backdropStyle={{backgroundColor:"rgba(0, 0, 0, 0.7)", borderWidth:0}}
          overlayStyle={{backgroundColor:'rgba(0, 0, 0, 0.0)', borderWidth:0, shadowColor:'rgba(0, 0, 0, 0.0)', shadowOpacity:0, shadowRadius:0, elevation:0}}>
            <View style={{width:imageSize, flexDirection:'row-reverse', alignSelf: 'center',}}>
              <ButtonIcon
              iconName={'close'}
              iconColor={'#FFF'}
              onPress={() => {this.setState({showReviewImages: false});}} />
            </View>
            <Carousel
            layout='default'
            ref={(c) => { this._carousel = c; }}
            data={review.imageUris}
            renderItem={this._renderItem}
            sliderWidth={width}
            itemWidth={imageSize}
            firstItem={this.state.imageIndexPressed}
            onSnapToItem={ (index) => this.setState({activeSlide:index}) }
            enableSnap={true}
            enableMomentum={false}
            />
            { this.pagination }
          </Overlay>
          }
        </>
      );
    }
    
    render(){
        const {review} = this.props;

        if(review.imageUris.length > 0){
          return (
              <>
              {this._createNewReview(review,true)}
              </>
          );
        } else {
          return (
            <>
            {this._createNewReview(review,false)}
            </>
          );
        }
    }
}

const styles = StyleSheet.create({
  containerMain: {
      flexDirection: 'column',
      marginTop: 12 ,
      padding: 16,
      borderRadius: 4,
      alignItems: 'flex-start',
      backgroundColor: '#fff',
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 3,
      },
      shadowOpacity: 0.29,
      shadowRadius: 4.65,
      elevation: 7,
  },
  containerMainAlt: {
    flexDirection: 'row',
    height: 200, //set for different screen sizes 3diff
    marginTop: 12 ,
    padding: 8, //set for different screen sizes 3diff
    borderRadius: 12,
    alignItems: 'flex-start',
    backgroundColor: '#fff',
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 3,
    },
    shadowOpacity: 0.29,
    shadowRadius: 4.65,
    elevation: 7,
},
  containerRatings: {
      flexDirection: 'row',
      width: '100%',
      marginTop: 4,
      alignSelf:'center',
      borderRadius: 4,
  },
  containerThumbnails: {
      flexDirection: 'row',
      width: '100%',
      marginBottom: 4,
  },
  btnCloseOverlay: {
    position: 'absolute',
    top: 8,
    left: 8,
    zIndex: 99
  },
  divider: {
    height: 0.1,
    width: '100%',
    marginTop: 4,
    marginBottom: 6,
    alignSelf: 'center',
    backgroundColor: '#EEE',
  },
  thumbnail: {
    height: 35,
    width: 35,
    marginRight: 4,
    borderRadius: 6
  }, 
  lowerRowContainer: {
    width: '100%', 
    flexDirection: 'row', 
    display: 'flex', 
    justifyContent: 'flex-start'
  },
  moreInfoContainer: {
    flex: 1,
    marginLeft: 'auto',
    alignItems: 'flex-end',
    alignSelf: 'flex-end'
  }
});

