
export function setFontTypeSourceSansPro(type){
    switch (type) {
        case 'black':
            return 'SourceSansPro_900Black';
        case 'bold':
            return 'SourceSansPro_700Bold';
        case 'semiBold':
            return 'SourceSansPro_600SemiBold';
        case 'light':
            return 'SourceSansPro_300Light';
        case 'extraLight':
            return 'SourceSansPro_200ExtraLight';
        default:
            return 'SourceSansPro_400Regular';
    }
};

export function setFontTypeMerriweather(type){
    switch (type) {
        case 'black':
            return 'Merriweather_900Black';
        case 'bold':
            return 'Merriweather_700Bold';
        case 'light':
            return 'Merriweather_300Light';
        default:
            return 'Merriweather_400Regular';
    }
};
