import React from 'react';
import {createStackNavigator} from '@react-navigation/stack';
import { MainNavigator } from './MainTabNavigator';
import {ViewLocationArea} from '../areas/ViewLocation';

const ProfileStack = createStackNavigator();

export function ProfileNavigator({navigator}){
    return (
        <ProfileStack.Navigator headerMode={'none'}>
            <ProfileStack.Screen name="User" component={MainNavigator}/>
            <ProfileStack.Screen name="ViewLocation" component={ViewLocationArea}/>
        </ProfileStack.Navigator>
    );
}