import React from 'react';
import {TextInput, StyleSheet, View} from 'react-native';

import {default as TextBody} from './TextBody';
import {InformationView} from './InformationView';
 
import validate from 'validate.js';

import {MaterialCommunityIcons} from '@expo/vector-icons';  


export class FormTextInput extends React.PureComponent {
    state = {
        constraints: null,
        errorText: '',
        showErrorText: false,
    };

    componentDidMount(){
        this.setState({
            constraints: this.props.constraints,
        });
    }

    _validate = (text) => {
        if(this.state.constraints !== undefined && this.state.constraints !== null){
            let errorJson = validate.single(text, this.state.constraints);
            if(errorJson === undefined || errorJson === null || errorJson === ''){
                this.setState({
                    showErrorText: false,
                    errorText: ''
                });    
                return;
            }
            this.setState({
                showErrorText: true,
                errorText: errorJson[0]
            });
        }
    };

    render(){
        return (
            <View style={styles.contain}>
                <TextInput 
                    multiline={this.props.isMultiline? true : false}
                    numberOfLines={this.props.isMultiline? this.props.numberOfLines : 1}     
                    defaultValue={this.props.defaultValue}
                    style={[{...this.props.styleTextInput,height:(this.props.isMultiline) ? 'auto' : 30},styles.textInput]}
                    secureTextEntry={this.props.secure}
                    placeholder={this.props.placeholder}
                    onChangeText={(text) => {this.props.onChangeText(text); this._validate(text)}}
                />
                {this.state.showErrorText &&
                    <InformationView isAlert={true} styleContainer={{marginVertical:8}} text={this.state.errorText} containerBackgroundColor={'#E14B1C'} hasShadow={false}/>
                }
            </View>
        );
    }
}

const styles =  StyleSheet.create({
    textInput: {
        width: '100%',
        padding: 4,
        borderWidth: 1,
        borderColor: '#CCDAE7',
        borderRadius: 4,
        backgroundColor: '#E9F0F6',
    },   
    contain: {
        width: '100%',
        marginBottom: 20,
    }
});
