import React from 'react';
import {View} from 'react-native'; 

import { default as TextBody} from './TextBody';

import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

export function BarIconItem(props){
    return (
        <View style={{flexDirection: props.containerOrientation , alignItems: props.alignLabel}}>
        <TextBody text={ props.iconLabel} color={ props.labelColor}/>
            <View style={{borderWidth: props.iconBorderWidth, borderColor: props.iconBorderColor, borderRadius: props.iconBorderRadius}}>
            <MaterialCommunityIcons name={ props.iconName} size={ props.iconSize} color={ props.iconColor}/>
            </View>
        </View>
    )
}