import React, {Component} from 'react';
import {View, Dimensions, StyleSheet, Text} from 'react-native';
import {Map, Marker, Popup, TileLayer} from 'react-leaflet';
import {Overlay} from 'react-native-elements';
import Modal from 'modal-react-native-web';
import Control from 'react-leaflet-control';

import {setRatingIconColor} from '../helpers/SetRatingIconColor';
import {ButtonTab} from '../components/ButtonTab';
import {ElementFilterPlate} from '../components/ElementFilterPlate';
import {BarIcon} from '../components/BarIcon';

import {graphqlAuth} from '../graphql/graphqlService';
import {getUserAuth} from '../graphql/sessionData';
import {createSearchBody} from '../graphql/queries';

import {wifiRatingConverter, seatingRatingConverter, noiseRatingCoverter, outletRatingConverter, distanceConverter} from '../helpers/Converters';


export class SearchLocationsMapArea extends Component {

  state = {
    data: [],
    filters: null,
    fuzzyName: null,
    index: 0,
    popup: null,
    zoom: 13,
    latitude: 0,
    longitude: 0,
    isClickableTimeout: 1000,
    isClickable: false,
    showFilterPlate: false
  }

  componentDidMount() {
    this.setState({
      popup: React.createRef(),
      latitude: this.props.route.params.latitude,
      longitude: this.props.route.params.longitude      
    }, () => {
      this._fetchLocations();
      setTimeout(() => {this._toggleIsClickable()}, this.state.isClickableTimeout);
    })
  }

  _toggleIsClickable = () => {
    this.setState({isClickable: !this.state.isClickable});  
  }

  _fetchLocations = () => {
    let body = createSearchBody(0, 50, this.state.fuzzyName, this.state.filters, this.state.latitude, this.state.longitude, 250);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
      (result) => {
        if(result.data === null){
          return;
        }
        let locationsModel = result.data.getLocationNearByElastic;
        let locations = locationsModel.locations;
        this.setState({
          data: [...this.state.data, ...locations],
        });
      },
      (error) => {
        Alert.alert(error.message);
      });
    });
  };

  _updateLocation = (viewport) => {
    this.setState({
      latitude: viewport.center[0],
      longitude: viewport.center[1],
      zoom: viewport.zoom
    });
  };

  _handleZoom = () => {
    this._closePopup();
  };

  _handleDrag = () => {
    this._closePopup();
  };

  _closePopup = () => {
    if(this.state.popup.current !== null){
      this.state.popup.current.leafletElement.options.leaflet.map.closePopup();
    }
  }

  _refreshLocations = () => {
    this.setState({
      data: []
    }, () => {
      this._fetchLocations()
    });
  };

  _viewList = () => {
    if(this.state.isClickable){
      this.props.navigation.goBack();
    }
  };

  _viewLocation = () => {
    this.props.navigation.navigate('ViewLocation', {locationId: this.state.data[this.state.index].id});
  };

  _setFuzzyName = (text) => {
    this.setState({fuzzyName: text});
  };

  _setFilters = (filters) => {
    this.setState({filters: filters});  
  };

  _toggleShowFilterPlate = () => {
    this.setState({showFilterPlate: !this.state.showFilterPlate});
  };

  _createMarker = (location, index) => {
    return (
      <Marker
        key={location.id}
        position={[
          location.latitude,
          location.longitude
        ]}
        onClick={() => {
          let popup =  React.createRef();
          this.setState({
            index: index, 
            popup: popup
          })
        }}
      >
        <Popup ref={this.state.popup} autoPan={false}>
          <div style={{cursor: 'pointer'}} onClick={() => {this._viewLocation()}}>
            <View style={styles.containerLocationInfo}>
              <View style={styles.containerRow}>
                <Text style={styles.labelDistance}>{distanceConverter(location.distance, 2)}mi</Text>
              </View>
              <View style={styles.containerRow}>
                <Text style={styles.labelLocationName}>{location.name}</Text>
              </View>
              <View style={styles.containerRow}>
                <BarIcon 
                  iconNames={[wifiRatingConverter(location.wifiRating),noiseRatingCoverter(location.noiseRating),seatingRatingConverter(location.seatingRating),outletRatingConverter(location.outletsRating)]}
                  iconColors={[setRatingIconColor(location.wifiRating),setRatingIconColor(location.noiseRating),setRatingIconColor(location.seatingRating),setRatingIconColor(location.outletsRating)]}
                />
              </View>
            </View>
          </div>
        </Popup>
      </Marker>);
  };

  render() {     
    const {height, width} = Dimensions.get('window');
    return (
      <View style={{
        flex: 1,
        flexDirection: 'column'
      }}>
        <link
          rel="stylesheet"
          href="https://unpkg.com/leaflet@1.6.0/dist/leaflet.css"
          integrity="sha512-xwE/Az9zrjBIphAcBb3F6JVqxf46+CDLwfLMHloNu6KEQCAWi6HcDUbeOfBIptF7tcCzusKFjFw2yuvEpDL9wQ=="
          crossOrigin=""
        />
        <Map
          center={[this.state.latitude, this.state.longitude]}
          zoom={this.state.zoom}
          zoomControl={false}
          onViewportChange={this._updateLocation}
          maxZoom={16}
          minZoom={10}
          style={{ 
            height: height,
            width: '100%'
          }}
        >
          {this.state.data.map((location, index) => this._createMarker(location, index))}
          <TileLayer  
              url='https://api.mapbox.com/styles/v1/mapbox/streets-v11/tiles/256/{z}/{x}/{y}?access_token=sk.eyJ1IjoidHJmb3JrbmVyIiwiYSI6ImNrY2llajEzbDBnYXcyeG1paHJ5MGd1cnQifQ.wsVkXLkINZUSGOrQctnbow'
              attribution='Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>'
            />
          <Control position='topleft'>
            <View style={{
                marginTop: 10,
                marginLeft: -10,
                flexDirection: 'column'
            }}>
              <div onClick={() => this._toggleShowFilterPlate()}>
                <ButtonTab
                  iconNameAdd={'magnify'}
                  iconName={'filter-variant'}
                  buttonBackgroundColor={'#FFFFFF'}
                  hasShadow={true}
                  leftTab={true}
                />
              </div>
              <View style={{height:6}} />              
              <div onClick={() => this._refreshLocations()}>
                <ButtonTab 
                  iconName={'refresh'}
                  buttonBackgroundColor={'#FFFFFF'}
                  hasShadow={true}
                  leftTab={true}
                />              
              </div>
              <View style={{height:6}} />              
              <div onClick={() => this._viewList()}>
                <ButtonTab 
                  iconName={'format-list-bulleted'}
                  buttonBackgroundColor={'#FFFFFF'}
                  hasShadow={true}
                  leftTab={true}
                />              
              </div>
            </View>
          </Control>
        </Map>
        <Overlay 
        ModalComponent={Modal} 
        isVisible={this.state.showFilterPlate} 
        onBackdropPress={() => {this._toggleShowFilterPlate();}}
        backdropStyle={{backgroundColor:"rgba(0, 0, 0, 0.7)", borderWidth:0}}
        overlayStyle={{width: (width>1000) ? '40%' : '85%', backgroundColor:'rgba(0, 0, 0, 0.0)', borderWidth:0, shadowColor:'rgba(0, 0, 0, 0.0)', shadowOpacity:0, shadowRadius:0, elevation:0}}>
          <ElementFilterPlate
            onPress={() => {this._refreshLocations(); this._toggleShowFilterPlate();}} 
            onClose={() => {this._toggleShowFilterPlate();}} 
            onChange={(filters) => this._setFilters(filters)}
            onChangeText={(text) => this._setFuzzyName(text)}
          />
        </Overlay>
      </View>

    );
  };
}

const styles = StyleSheet.create({
  containerLocationInfo: {
    flexDirection: 'column',
    borderRadius: 4,
    alignItems: 'flex-start',
    alignSelf: 'center',
    backgroundColor: '#fff',
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 3,
    },
    shadowOpacity: 0.29,
    shadowRadius: 4.65,
    elevation: 7,
  },
  containerAddress: {
    flexDirection: 'column',
    textAlign: 'left'
  },
  containerRow: {
    flexDirection: 'row',
    textAlign: 'left',
    padding: 6
  },
  labelLocationName: {
    fontWeight: '700',
  },
  labelDistance: {
    fontWeight: '600',
  },
});