import React from 'react';
import {createStackNavigator} from '@react-navigation/stack';
import {MainNavigator} from './MainTabNavigator';
import {SearchLocationsMapArea} from '../areas/SearchLocationsMap';
import {ViewLocationArea} from '../areas/ViewLocation';
import {AddReviewArea} from '../areas/AddReview';
import { ReportLocationArea, ReportReviewArea } from '../areas/Report';
import {AddLocationFormArea} from '../areas/AddLocationForm';

const HomeStack = createStackNavigator();

export function HomeNavigator({navigator}){
    return (
        <HomeStack.Navigator headerMode={'none'}>
            <HomeStack.Screen name="SearchLocations" component={MainNavigator}/>
            <HomeStack.Screen name="SearchLocationsMap" component={SearchLocationsMapArea}/>
            <HomeStack.Screen name="ViewLocation" component={ViewLocationArea}/>
            <HomeStack.Screen name="ReportLocation" component={ReportLocationArea}/>
            <HomeStack.Screen name="ReportReview" component={ReportReviewArea}/>
            <HomeStack.Screen name="AddReview" component={AddReviewArea}/>
            <HomeStack.Screen name="AddLocationForm" component={AddLocationFormArea}/>
        </HomeStack.Navigator>
    );
}