import React, { Component } from 'react';
import {View, Text, Image, StyleSheet} from 'react-native';

import {default as TextBody} from '../components/TextBody';

import * as ImagePicker from 'expo-image-picker';
import Constants from 'expo-constants';
import * as Permissions from 'expo-permissions';
import Carousel from 'react-native-snap-carousel';
import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

export class AddReviewImageArea extends Component {
    state = {
        index: 0,
        max: 3,
        images: [null, null, null]
    };

    componentWillMount(){
        this._onUpdate();
    }

    render() {
        return (
            <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
            <Carousel
                layout='default'
                ref={(c) => { this._carousel = c; }}
                data={this.state.images}
                renderItem={this._renderItem}
                sliderWidth={ 500 }
                itemWidth={ 300 }
                onSnapToItem={ index => this.setState({index:index}) }
                />
            </View>
        );
    }

    componentDidMount() {
        this._getPermissionAsync();
    }

    _renderItem = ({item, index}) => {
        return (
            <View style={{
                backgroundColor:'#222',
                borderRadius: 5,
                height: 300}}>
                {
                    item !== null ? 
                    (this._createImageView(item)) : 
                    (this._createAddImage())
                }
            </View>
        );
    }

    _createImageView = (item) => {
        return (
            <View style={styles.container}>
                <View>
                    <Image source={{ uri: item.uri }} style={{ width: 300, height: 300 }} />    
                </View>
                <View style={styles.elevated}>
                    <MaterialCommunityIcons name="close-circle" onPress={this._removeImage} />
                </View>
            </View>
        );
    };

    _createAddImage = () => {
        return (
            <View style={styles.card}>
                <MaterialCommunityIcons name="plus-circle" title="Pick an image from camera roll" color={'#FFF'} size={24} onPress={this._pickImage} />
                <TextBody text={"Add an image"} color={"#FFF"}/>
            </View>
        );
    };

    _getPermissionAsync = async () => {
        if (Constants.platform.ios || Constants.platform.android) {
            const { status } = await Permissions.askAsync(Permissions.CAMERA_ROLL);
            if (status !== 'granted') {
                alert('Sorry, we need camera roll permissions to make this work!');
            }
        }
    };

    _pickImage = async () => {
        try {
            let result = await ImagePicker.launchImageLibraryAsync({
                mediaTypes: ImagePicker.MediaTypeOptions.Images,
                allowsEditing: true,
                aspect: [1, 1],
                quality: 1,
            });
            if (!result.cancelled) {
                const newImages = [...this.state.images];
                newImages[this.state.index] = result;
                this.setState({
                    images: newImages 
                }, this._onUpdate);
            }
        } catch (E) {
            alert('Sorry, we need camera roll permissions to make this work!');
        }
    };

    _removeImage = () => {
        const newImages = [...this.state.images];
        newImages[this.state.index] = null;
        this.setState({
            images: newImages 
        }, this._onUpdate);
    };

    _onUpdate = () => {
        this.props.onChange(this.state.images);
    };
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        flexDirection: 'column'
    },
    card: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center'
    },
    elevated: {
        ...StyleSheet.absoluteFillObject,
        alignSelf: 'flex-end'
    }
  });