import React, { Component } from "react";

import { ButtonGroup } from 'react-native-elements';

import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

export class RatingButtonGroup extends Component{
    
    constructor(){
        super();
        this.state = {
            selected: 0
        };
    }

    componentDidMount() {
        this._onPress(0);
    }

    _onPress = (selectedIndex) => {
        this.setState({selected: selectedIndex}, 
            this.props.onPress(this.props.ratings[selectedIndex]));
    }

    _createButtonGroup = (icons) => {
        var buttons = [];
        icons.forEach(name => {
            var element = () => <MaterialCommunityIcons size={24} name={name}/>;
            buttons.push({
                element: element
            });
        });
        return buttons;
    };

    render(){
        const buttons = this._createButtonGroup(this.props.icons);
        const containerWidth = this.props.containerWidth;
        return(
            <ButtonGroup
                containerStyle={{width:containerWidth, marginLeft:0}}
                selectedButtonStyle={{backgroundColor: "#9DB492"}}
                selectedIndex={this.state.selected}
                innerBorderStyle={{color:"rgba(0, 0, 0,0.0)", width:0}}
                buttons={buttons}
                onPress={this._onPress}
                />
        )
    };
}