import React from 'react';
import {createStackNavigator} from '@react-navigation/stack';
import {MainNavigator} from './MainTabNavigator';
import {AddLocationFormArea} from '../areas/AddLocationForm';

const AddLocationStack = createStackNavigator();

export function AddLocationNavigator({navigator}){
    return (
        <AddLocationStack.Navigator headerMode={'none'}>
            <AddLocationStack.Screen name="AddLocation" component={MainNavigator}/>
            <AddLocationStack.Screen name="AddLocationForm" component={AddLocationFormArea}/>
        </AddLocationStack.Navigator>
    );
}