import { AsyncStorage } from 'react-native';

async function getUserAuth(){
    return await AsyncStorage.getItem('fernToken');
}

async function getUserId(){
    return await AsyncStorage.getItem('fernUserId');
}

async function getUserEmail(){
    return await AsyncStorage.getItem('fernEmail');
}

async function setUserAuth(data){
    await AsyncStorage.multiSet(
        [['fernToken', data.login.token], 
        ['fernUserId', data.login.username], 
        ['fernEmail', data.login.email]], null);
}

async function removeUserAuth(){
    await AsyncStorage.removeItem('fernToken');
}

async function getLatLng(){
    return await AsyncStorage.getItem('fernCoords');
}

async function setLatLng(position) {    
    await AsyncStorage.setItem('fernCoords', position.coords);
}

export {setUserAuth, getUserAuth, getUserId, getUserEmail, removeUserAuth, getLatLng, setLatLng}
