function createRegistrationBody(email, username, password, accepted){
    let variables = {
        userModel: {
            email: email,
            username : username,
            password : password,
            accepted : accepted
        }
    };
    let body = {
        query: 'mutation ($userModel: UserModel!){createUser(userModel: $userModel){success}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createVerificationBody(email, verificationCode){
    let variables = {
        verificationModel: {
            email: email,
            verificationCode : verificationCode
        }
    };
    let body = {
        query: 'mutation ($verificationModel: VerificationModel!){verifyEmail(verificationModel: $verificationModel){success}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createPasswordResetVerificationBody(email, password, verificationCode){
    let variables = {
        passwordResetModel: {
            email: email,
            password: password,
            verificationCode : verificationCode
        }
    };
    let body = {
        query: 'mutation ($passwordResetModel: PasswordResetModel!){resetPassword(passwordResetModel: $passwordResetModel){success}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createPasswordResetEmailBody(email){
    let body = {
        query: 'mutation {createPasswordResetEmail(email: \"' + email + '\"){success}}'
    };
    return body;
};

function createAddLocationBody(name, streetAddress, city, state, zipCode){
    let variables = {
        locationInputModel: {
            name: name,
            streetAddress: streetAddress,
            city: city,
            state: state,
            zipCode: zipCode
        }
    };
    let body = {
        query: 'mutation ($locationInputModel: LocationInputModel!){createNewLocation(locationModel: $locationInputModel){success id}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createAddReviewBody(locationId, noise, wifi, seating, outlets, title, review){
    let variables = {
        reviewInputModel: {
            locationId: locationId,
            noise: noise,
            wifi: wifi,
            seating: seating,
            outlets: outlets,
            title: title,
            review: review
        }
    };
    let body = {
        query: 'mutation ($reviewInputModel: ReviewInputModel!){createNewReview(reviewModel: $reviewInputModel){success id}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createReportLocationBody(locationId, text){
    let variables = {
        reportModel: {
            locationId: locationId,
            text: text
        }
    };
    let body = {
        query: 'mutation ($reportModel: ReportLocationInputModel!){reportLocation(reportModel: $reportModel){success id}}',
        variables: JSON.stringify(variables)
    };
    return body;
}

function createReportReviewBody(locationId, reviewId, text){
    let variables = {
        reportModel: {
            locationId: locationId,
            reviewId: reviewId,
            text: text
        }
    };
    let body = {
        query: 'mutation ($reportModel: ReportReviewInputModel!){reportReview(reportModel: $reportModel){success id}}',
        variables: JSON.stringify(variables)
    };
    return body;
}

export {createRegistrationBody,
    createVerificationBody, 
    createPasswordResetVerificationBody, 
    createPasswordResetEmailBody, 
    createAddLocationBody, 
    createAddReviewBody,
    createReportLocationBody,
    createReportReviewBody
};