import React, {Component} from 'react';
import {Animated, Easing, View, StyleSheet, Image, SafeAreaView, FlatList, Dimensions, Linking} from 'react-native';

import { default as TextTitle} from '../components/TextTitle';
import { default as TextBody} from '../components/TextBody';
import {ButtonIcon} from '../components/ButtonIcon';
import { ButtonTab } from '../components/ButtonTab';
import {BarIcon} from '../components/BarIcon';
import {ReviewCard} from '../components/ReviewCard';

import {graphqlAuth} from '../graphql/graphqlService';
import {getUserAuth} from '../graphql/sessionData';
import {createGetReviewByLocationBody, createGetLocationAndReviewsByLocationBody} from '../graphql/queries';
import style from '../styles/Container.Form.Style'; 

import { wifiRatingConverter, noiseRatingCoverter, seatingRatingConverter, outletRatingConverter, distanceConverter } from '../helpers/Converters'
import { setRatingIconColor } from '../helpers/SetRatingIconColor';

const { width, height } = Dimensions.get('window');
const hlfWidth = (width / 2);
const hlfHeight = (height / 2);
const isDesktop = width>1000;

let displayImageWidth; 
let displayImageHeight;
let displayImageMarginTop;
let containerInfoWidth;
let containerInfoPadding;

function choseStyle(width){
    if(isDesktop){
        displayImageWidth = width - 40;
        displayImageHeight = hlfHeight - 40;
        displayImageMarginTop = 20;
        containerInfoWidth = width-320;
        containerInfoPadding = 32;
    } else {
        displayImageWidth = width;
        displayImageHeight = hlfHeight;
        displayImageMarginTop = 0;
        containerInfoWidth = width-40;
        containerInfoPadding = 12;
    }
  }

choseStyle(width);
  
export class ViewLocationArea extends Component {
    
    state = {
        data: [],
        start: 0,
        max: 5,
        location: {},
        reachedEnd: false,
        enableScrollViewScroll: true,
        translateYValue: new Animated.Value(15),
    };

    componentDidMount() {
        const { locationId } = this.props.route.params;
        this._fetchLocation(locationId);
        this.fadeIn();
    }

    fadeIn = () => {
        Animated.timing(this.state.translateYValue, {
          toValue: 0,
          easing: Easing.elastic(1),
          duration: 2000
        }).start();
    };

    _fetchLocation = (locationId) => {
        let body = createGetLocationAndReviewsByLocationBody(0, this.state.max, locationId);
        getUserAuth().then((token) => {graphqlAuth(token, body).then(
            (result) => { 
                if(result.data === null){
                    return;
                }
                this.setState({location: result.data.getLocationById})
                this._updateReviewsState(result.data.getReviewsByLocationId);
            },
            (error) =>{
                //do nothing
            }
        )});
    };

    _fetchReviews = (locationId) => {
        if(this.state.reachedEnd){
          return;
        }
        let body = createGetReviewByLocationBody(this.state.start, this.state.max, locationId);
        getUserAuth().then((token) => {graphqlAuth(token, body).then(
          (result) => {
            if(result.data === null){
              return;
            }
            this._updateReviewsState(result.data.getReviewsByLocationId);
          },
          (error) => {
    
          });
        });
    };

    _updateReviewsState = (reviewsModal) => {
        let paging = reviewsModal.paging;
        let reviews = reviewsModal.reviews;
        this.setState((prevState, nextProps) => ({
          data: [...this.state.data, ...reviews],
          reachedEnd: paging.stop - paging.start !== this.state.max
        }));
    }

    _handleLoadMore = (locationId) => {
        this.setState(
            (prevState, nextProps) => ({
            start: prevState.start + this.state.max,
            }),
            () => {
                this._fetchReviews(locationId);
            }
        );
    };

    _addReview = (locationId, locationName) => {
        this.props.navigation.navigate('AddReview', 
        {locationId: locationId, locationName: locationName});
    };

    _reportLocation = (locationId, locationName) => {
        this.props.navigation.navigate('ReportLocation',
        {locationId: locationId, locationName: locationName});
    }

    _getDirections = (url) => {
        Linking.openURL(url).catch((error) => console.error('An error occurred', error));
    }

    _createControlPanel = (location) => {
        return(
            <>
            <ButtonTab
            iconName={"arrow-left-bold"}
            buttonBackgroundColor={'#FFF'}
            hasShadow={true}
            leftTab={true}
            onPress={() => this.props.navigation.goBack()}/>
            <View style={{height:6}} />
            <ButtonTab
            iconName={'directions'}
            buttonBackgroundColor={'#FFF'}
            hasShadow={true}
            leftTab={true}
            onPress={() => this._getDirections('https://www.google.com/maps/search/?api=1&query='+encodeURI(location.name)+'&query_place_id=' + location.id)}
            />
            <View style={{height:6}} />
            <ButtonTab
            iconName={"pencil"}
            buttonBackgroundColor={'#9DB492'}
            hasShadow={true}
            leftTab={true}
            onPress={() => this._addReview(location.id, location.name)}
            />
            <View style={{height:6}} />
            <ButtonTab
            iconName={"flag-variant"}
            buttonBackgroundColor={'#FF0033'}
            hasShadow={true}
            leftTab={true}
            onPress={() => this._reportLocation(location.id, location.name)}
            />
            </>
        );
    }

    _createMainDesktopBody = (location) => {
        return(
            <SafeAreaView style={styleDesktop.containerBody}>
            <View style={styleDesktop.containerDisplayImage}>
                <Image source={{uri : location.imageUri}} style={styleDesktop.displayImage} resizeMode="cover" />
            </View>
            <View style={{width:'55%'}} />
            <FlatList
            ListHeaderComponent={
                <View style={{width:'55%', alignSelf:'flex-end'}}> 
                <View>  
                    <View style={styleDesktop.containerLocationInfo}>
                        <TextBody style={{letterSpacing:1}} text={"// LOCATION"} weight={'semiBold'} color={"#9DB492"}/>
                        <View style={{height:40}}></View>
                        <TextBody text={location.name} weight={'bold'} fontSize={60} />
                        <View style={styleDesktop.containerAddress}>
                            <TextTitle text={location.streetAddress + " " + location.city + ", " + location.state + " " + location.zipCode} fontSize={16} color={'#BBB'}/>
                        </View>
                        <View style={styleDesktop.containerRatings}>
                            <BarIcon 
                            iconNames={[wifiRatingConverter(location.wifiRating),noiseRatingCoverter(location.noiseRating),seatingRatingConverter(location.seatingRating),outletRatingConverter(location.outletsRating)]}
                            iconColors={[setRatingIconColor(location.wifiRating),setRatingIconColor(location.noiseRating),setRatingIconColor(location.seatingRating),setRatingIconColor(location.outletsRating)]}
                            iconSize = {100}
                            iconBorderWidth = {0}
                            iconBorderColor = {'#dddddd'}
                            iconBorderRadius = {4}
                            />
                        </View>
                        <View style={{position:'absolute', width:24, height:24, top: 24, right: 24, borderRadius: 150, backgroundColor:"#E14B1C"}} />
                    </View>
                    <View style={styleDesktop.containerReviewFlatListLabel}>
                        <TextBody text={'REVIEWS'} weight={'semiBold'} fontSize={14} color={'#444444'}/>
                    </View>
                </View>
                </View> 
            }
            ListFooterComponent={ <View style={styleDesktop.listFooter} />}
            style = {styleDesktop.containerFlatList}
            data={this.state.data}
            keyExtractor={item => item.id}
            renderItem={({ item }) =>
                    <View style={{width:'55%', alignSelf:'flex-end'}}> 
                        <ReviewCard review={item}/>
                    </View>
            }
            onEndReached={() => {this._handleLoadMore(location.id);}}
            onEndReachedThreshold={0.5}
            initialNumToRender={1}/>
            <View style={styleDesktop.containerFloatingButtons}>
                {this._createControlPanel(location)}
            </View>
            </SafeAreaView>
        );
    }

    _createMainMobileBody = (location) => {
        return(
            <SafeAreaView style={style.containerBody}>
            <FlatList
            ListHeaderComponent={
                <View style={styles.containerListHeader}>  
                    <Image source={{uri : location.imageUri}} style={styles.displayImage} resizeMode="cover" />
                    <View style={styles.containerLocationInfo}>
                        <TextTitle text={location.name} weight={'bold'} fontSize={24} />
                        <View style={styles.containerAddress}>
                            <TextBody text={location.streetAddress + " " + location.city + ", " + location.state + " " + location.zipCode} fontSize={14} color={'#777777'}/>
                        </View>
                        <View style={style.divider} />
                        <View style={styles.containerRatings}>
                            <BarIcon 
                            iconNames={[wifiRatingConverter(location.wifiRating),noiseRatingCoverter(location.noiseRating),seatingRatingConverter(location.seatingRating),outletRatingConverter(location.outletsRating)]}
                            iconColors={[setRatingIconColor(location.wifiRating),setRatingIconColor(location.noiseRating),setRatingIconColor(location.seatingRating),setRatingIconColor(location.outletsRating)]}
                            iconSize = {60}
                            iconBorderWidth = {0}
                            iconBorderColor = {'#dddddd'}
                            iconBorderRadius = {4}
                            />
                        </View>
                    </View>
                    <View style={styles.containerReviewFlatListLabel}>
                        <TextBody text={'REVIEWS'} weight={'semiBold'} fontSize={14} color={'#444444'}/>
                    </View>
                </View> 
            }
            ListFooterComponent={ <View style={styles.listFooter} />}
            style = {styles.containerFlatList}
            data={this.state.data}
            keyExtractor={item => item.id}
            renderItem={({ item }) => 
                <ReviewCard review={item}/>
            }
            onEndReached={() => {this._handleLoadMore(location.id);}}
            onEndReachedThreshold={0.5}
            initialNumToRender={1}/>
            <View style={styles.containerFloatingButtons}>
                {this._createControlPanel(location)}
            </View>
            </SafeAreaView>
        );
    }

    render(){
        const { location } = this.state;
        if(isDesktop){
            return (this._createMainDesktopBody(location));
        } else {
            return (this._createMainMobileBody(location));
        }
    }
};

const styleDesktop = StyleSheet.create({
    containerBody: {
        flex: 1,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: '#F8F7F0',
    },
    containerFlatList: {
        position: 'absolute',
        flexDirection: 'column',
        width: '100%',
        height: '100%',
        right: 0,
    },
    containerDisplayImage:{
        width: '45%',
        height: '100%',
        padding: 20,
        backgroundColor: "#F8F7F0",
    },
    displayImage: {
        width: '100%',
        height: '100%',
        borderRadius: 8,
        backgroundColor: "#9DB492",
    },
    containerLocationInfo: {
        flexDirection: 'column',
        padding: 24,
        marginTop: 20,
        marginLeft: 20,
        marginRight: 20,
        borderRadius: 8,
        alignItems: 'flex-start',
        backgroundColor: '#fff',
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 3,
        },
        shadowOpacity: 0.29,
        shadowRadius: 4.65,
        elevation: 7,
    },
    containerAddress: {
        flexDirection: 'row',
        alignItems: 'flex-start',
    },
    containerRatings: {
        flexDirection: 'row',
        width: '100%',
        marginTop: 4,
        marginBottom: 4,
        alignSelf:'center',
    },
    containerFloatingButtons: {
        position: 'absolute',
        top: 20,
        left: 0,
    },
    containerReviewFlatListLabel: {
        marginTop: 16,
        marginLeft: 20,
        alignItems: 'flex-start',
    },
    listFooter: {
        height: 150
    },
});


const styles = StyleSheet.create({
    containerFlatList: {
        flexDirection: 'column',
        width: width,
        height: height,
        alignSelf: 'center',
        backgroundColor: '#F8F7F0',
    },
    displayImage: {
        position: 'absolute',
        width: displayImageWidth,
        height: displayImageHeight,
        marginTop: displayImageMarginTop,
        alignSelf: 'center',
        backgroundColor: "#9DB492",
        borderRadius: 4,
    },
    containerLocationInfo: {
        flexDirection: 'column',
        width: containerInfoWidth,
        padding: containerInfoPadding,
        marginTop: hlfHeight - 50,
        borderRadius: 4,
        alignItems: 'flex-start',
        alignSelf: 'center',
        backgroundColor: '#fff',
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 3,
        },
        shadowOpacity: 0.29,
        shadowRadius: 4.65,
        elevation: 7,
    },
    containerAddress: {
        flexDirection: 'row',
        alignItems: 'flex-start',
    },
    containerRatings: {
        flexDirection: 'row',
        width: '100%',
        marginTop: 4,
        marginBottom: 4,
        alignSelf:'center'
    },
    containerFloatingButtons: {
        position: 'absolute',
        top: 20,
        left: 0,
    },
    containerReviewFlatListLabel: {
        width: containerInfoWidth,
        paddingTop: 12,
        alignItems: 'flex-start',
        alignSelf: 'center',
    },
    listFooter: {
        height: 150
    },
    ratingSpacers: {
        width:6
    }
});
