import { StyleSheet, Dimensions } from 'react-native';

const { width, height } = Dimensions.get('window');
const trdWidth = (width/3);

let containerMainWidth;

function choseStyle(width){
  if(width > 1000){
    containerMainWidth = trdWidth
  } else {
    containerMainWidth = width - 40
  }
}

choseStyle(width);

export default StyleSheet.create({
    containerBody: {
      flex: 1,
      backgroundColor: '#F8F7F0',
      alignItems: 'center',
      justifyContent: 'center',
    },
    containerMain: {
      flexDirection: 'column',
      width: containerMainWidth,
      padding: 16,
      borderRadius: 4,
      alignItems: 'center',
      backgroundColor: '#fff',
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 3,
      },
      shadowOpacity: 0.29,
      shadowRadius: 4.65,
      elevation: 7,
    },
    containerTextInputLabels: {
      width: '100%',
      flexDirection: 'row',
      justifyContent: 'space-between',
    },
    textLabel: {
      marginRight: 'auto',
      marginBottom: 4,
      fontSize: 12,
      color: '#555555'
    },
    divider: {
      height: 0.1,
      width: '100%',
      marginTop: 8,
      marginBottom: 8,
      alignSelf: 'center',
      backgroundColor: '#dddddd'
    },
    defaultShadow: {
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 3,
      },
      shadowOpacity: 0.29,
      shadowRadius: 4.65,
      elevation: 7,
    },
  });

