import React from 'react';
import {View, TextInput, TouchableOpacity, StyleSheet, Text} from 'react-native'; 
import {MaterialCommunityIcons as Icon} from '@expo/vector-icons';
import {validate} from 'validate.js';

export class TextInputSecure extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            constraints: props.constraints,
            errorText: '',
            showErrorText: false,
            secureTextEntry: true,
            iconName: "eye"
        }
    }

    onIconPress = () => {
        let iconName = (this.state.secureTextEntry) ? "eye-off":"eye";

        this.setState({
            secureTextEntry: !this.state.secureTextEntry,
            iconName: iconName
        });
    }

    _validate = (text) => {
        if(this.state.constraints !== undefined && this.state.constraints !== null){
            let errorJson = validate.single(text, this.state.constraints);
            if(errorJson === undefined || errorJson === null || errorJson === ''){
                this.setState({
                    showErrorText: false,
                    errorText: ''
                });    
                return;
            }
            this.setState({
                showErrorText: true,
                errorText: errorJson[0]
            });
        }
    };

    render(){
        return (
            <View style={styles.contain}>
                <View style={styles.textInputSecure}>
                    <TextInput 
                        style = {{flex:1, padding: 4}}
                        secureTextEntry={this.state.secureTextEntry}
                        onChangeText={(text) => {this.props.onChangeText(text); this._validate(text);}}/>
                    <TouchableOpacity onPress={this.onIconPress}>
                        <Icon name={this.state.iconName} style={styles.iconVisibility} size={20} />
                    </TouchableOpacity>
                </View>
                {this.state.showErrorText && <Text style={styles.textError} >{this.state.errorText}</Text>}
            </View>
        );
    }
}

const styles =  StyleSheet.create({
    textInputSecure: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        width: '100%',
        borderWidth: 1,
        borderColor: '#CCDAE7',
        borderRadius: 4,
        backgroundColor: '#E9F0F6',
    }, 
    contain: {
        width: '100%',
        marginBottom: 20,
    },
    textError: {
        borderBottomLeftRadius: 4,
        borderBottomRightRadius: 4,
        backgroundColor: '#B64C4C',
    },
    iconVisibility: {
        position: 'absolute',
        right: 4,
        marginTop: 1
    }
});