import React from 'react';
import {View, TouchableOpacity} from 'react-native'; 
import {MaterialCommunityIcons} from '@expo/vector-icons'; 

export class ButtonTab extends React.Component {

    render(){
        return (
            <TouchableOpacity style={{...this.props.styleContainer,}} onPress={this.props.onPress}>
                <View style={{
                ...this.props.styleButton,
                flexDirection: 'row',        
                height: (this.props.buttonSize != null) ? this.props.buttonSize : 32,
                width: (this.props.iconNameAdd != null) ? ((this.props.buttonSize != null) ? this.props.buttonSize : 77) : 42,
                borderTopLeftRadius:(this.props.rightTab == true) ? 4 : 0,
                borderTopRightRadius:(this.props.leftTab == true) ? 4 : 0,
                borderBottomLeftRadius:(this.props.rightTab == true) ? 4 : 0,
                borderBottomRightRadius:(this.props.leftTab == true) ? 4 : 0,
                backgroundColor: (this.props.buttonBackgroundColor != null) ? this.props.buttonBackgroundColor : 'transparent',
                justifyContent: 'center',
                alignItems: 'center',
                shadowColor: "#222222",
                shadowOffset: {
                  width: 0,
                  height: 3,
                },
                shadowOpacity: this.props.hasShadow ? 0.29 : 0.0,
                shadowRadius: this.props.hasShadow ? 4.65 : 0,
                elevation: this.props.hasShadow ? 4 : 0,
                zIndex: 99, 
                }}>
                    <MaterialCommunityIcons 
                    name={this.props.iconNameAdd} 
                    size={(this.props.iconNameAdd != null) ? ((this.props.iconSize != null) ? this.props.iconSize : 24) : 0} 
                    color={(this.props.iconNameAdd != null) ? ((this.props.iconColor != null) ? this.props.iconColor : "#222") : "#222"}/>
                    <View style={{
                        height: '60%',
                        width:(this.props.iconNameAdd != null) ? 1 : 0,
                        marginLeft: (this.props.iconNameAdd != null) ? 4 : 0, 
                        marginRight: (this.props.iconNameAdd != null) ? 4 : 0,
                        borderRadius: 2,  
                        backgroundColor: "#CCC"
                    }}/> 
                    <MaterialCommunityIcons 
                    name={this.props.iconName} 
                    size={(this.props.iconSize != null) ? this.props.iconSize : 24} 
                    color={(this.props.iconColor != null) ? this.props.iconColor : "#222"}/>
                </View>
            </TouchableOpacity>
        );
    }
}