import React from 'react';
import {View, StyleSheet, TouchableOpacity} from 'react-native'; 

import TextBody from '../components/TextBody';

export function ButtonText(props){
        const {styleContainer,hasContainer,onPress,styleButtonContainer,buttonBorderRadius,text,fontSize,weight,textColor} = props;
        return (
            <TouchableOpacity style={{...styleContainer}} onPress={onPress}>
                <View style={[{...styleButtonContainer, 
                    borderRadius: buttonBorderRadius}, (hasContainer)? styles.buttonTextContainer : {}]}>
                    <TextBody text={text} fontSize={fontSize} weight={weight} color={textColor}/>
                </View>
            </TouchableOpacity>
        );
}

ButtonText.defaultProps = {
    textColor: '#222',
    weight: "regular",
    buttonBorderRadius: 4,
}

const styles =  StyleSheet.create({
    buttonTextContainer: {
        paddingLeft: 10,
        paddingRight: 10,
        paddingTop: 8,
        paddingBottom: 8,
        alignItems: 'center',
    }
});