import React from 'react';
import {View, TouchableOpacity} from 'react-native'; 

import {default as TextBody} from '../components/TextBody';

import {MaterialCommunityIcons} from '@expo/vector-icons'; 

export class ButtonCTA extends React.Component {

    render(){
        return (
            <TouchableOpacity 
            style={{
                ...this.props.styleContainer,
                width: (this.props.buttonSize != null) ? this.props.buttonSize : 128,
                height: (this.props.iconNameAdd != null) ? ((this.props.buttonSize != null) ? this.props.buttonSize : 64) : 32,
                backgroundColor: (this.props.buttonBackgroundColor != null) ? this.props.buttonBackgroundColor : 'transparent',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: (this.props.buttonBorderRadius != null) ? this.props.buttonBorderRadius : 4,
                zIndex: 99
            }}
            onPress={this.props.onPress}>
                <View style={{
                ...this.props.styleIcon,
                flexDirection: 'row',        
                shadowColor: "#222222",
                shadowOffset: {
                  width: 0,
                  height: 3,
                },
                shadowOpacity: this.props.hasShadow ? 0.29 : 0.0,
                shadowRadius: this.props.hasShadow ? 4.65 : 0,
                elevation: this.props.hasShadow ? 4 : 0, 
                }}>
                    <MaterialCommunityIcons 
                    name={this.props.iconName} 
                    size={(this.props.iconSize != null) ? this.props.iconSize : 24} 
                    color={(this.props.iconColor != null) ? this.props.iconColor : "#222"}/>
                    <TextBody text={this.props.text} fontSize={this.props.fontSize} weight={'semiBold'} color={'#ffffff'}/> 
                </View>
            </TouchableOpacity>
        );
    }
}