import React from 'react';
import { ScrollView, StyleSheet, View } from 'react-native'; 
import Autocomplete from 'react-native-autocomplete-input';
import { ButtonPrimary } from './ButtonPrimary';

export class AutocompleteTextInput extends React.Component {
    
    state = {
        value: '',
        hideResults: false,
        data: this.props.data,
        filteredData: []
    };

    _onChangeValue = (text) => {
        this.setState({
                value: text,
                hideResults: false,
                filteredData: this._filterData(text)
            }, 
            () => {
                this.props.onChangeText(text);
            });
    };

    _onChangeValueClick = (text) => {
        this.setState({
                value: text,
                hideResults: true,
                filteredData: this._filterData(text)
            }, 
            () => {
                this.props.onChangeText(text);
            });
    };

    _filterData(query){
        if (query === '') {
            return [];
          }
          const regex = new RegExp(`${query.trim()}`, 'i');
          return this.state.data.filter(data => data.search(regex) >= 0);
    };
    
    render(){
        return (
            <View style={styles.container}>
                <Autocomplete
                    style={styles.textInput}
                    data={this.state.filteredData}
                    autoCorrect={true}
                    hideResults={this.state.hideResults}
                    value={this.state.value}
                    placeholder={this.props.placeholder}
                    onChangeText={text => this._onChangeValue(text)}
                    renderItem={({ item, i }) => (
                    <ButtonPrimary
                        text={item}
                        buttonBorderRadius={0}
                        onPress={() => this._onChangeValueClick(item)}
                    />
                    )}
                />
            </View>
        );
    }
}   

const styles =  StyleSheet.create({
    container: {
        width: '100%',
        height: 30,
        marginBottom: 20,
        zIndex: 99
    }, textInput: {
        width: '100%',
        padding: 4,
        borderWidth: 1,
        borderColor: '#CCDAE7',
        borderRadius: 4,
        backgroundColor: '#E9F0F6',
    },
});