import React,{useRef, useState} from 'react';
import {Animated, Easing, View, TouchableOpacity} from 'react-native';

import {ButtonText} from '../components/ButtonText';
import {default as TextBody} from '../components/TextBody';
import {ButtonIcon} from '../components/ButtonIcon';

import { MaterialCommunityIcons} from '@expo/vector-icons'; 


export const MenuToggleableItem = (props) => {
    
    const translateYValue = useRef(new Animated.Value(12)).current;
    const opacityValue = useRef(new Animated.Value(0)).current;

    React.useEffect(() => {
        _menuIn();

        return () => {
        }
      }, []);

      const _menuIn = () => {
        Animated.parallel([
            Animated.timing(translateYValue,{
                  toValue: 0,
                  delay: props.itemDelay,
                  easing: Easing.elastic(1),
                  duration: 420,
            }),
            Animated.timing(opacityValue,{
                  toValue: 1,
                  delay: props.itemDelay,
                  duration: 320,
            })
        ]).start()
      };
        
        return (
            <Animated.View style={{
                width:'100%', 
                flexDirection:'row',
                opacity: opacityValue,
                transform: [{ translateY: translateYValue }]}}>
                <MaterialCommunityIcons name={props.itemIcon} size={16}/>
                <View style={{width: 8}} />
                    <TouchableOpacity onPress={props.onMenuItemPress}>
                        <TextBody text={props.itemText}/>
                    </TouchableOpacity>
                <View style={{width: 16}} />
            </Animated.View>
        );
}