import React,{useRef, useState} from 'react';
import {Animated, Easing, View, TouchableOpacity} from 'react-native';

import {ButtonIcon} from '../components/ButtonIcon';

import style from '../styles/Container.Form.Style';


export const MenuToggleable = (props) => {

    const translateYValue = useRef(new Animated.Value(-10)).current;
    const opacityValue = useRef(new Animated.Value(0)).current;

    const [menuHeight, setMenuHeight] = useState(0);
    const [menuHidden, menuToggle] = useState(true);

    React.useEffect(() => {
        _menuIn();

        return () => {
        }
      }, []);

      const _menuIn = () => {
        Animated.parallel([
            Animated.timing(translateYValue,{
                  toValue: 0,
                  easing: Easing.bezier(0.645, 0.045, 0.355, 1),
                  duration: 220,
            }),
            Animated.timing(opacityValue,{
                  toValue: 1,
                  duration: 120,
            })
        ]).start()
      };
    
        
        return (
            <View style={{...props.styleContainer, position:'absolute', alignItems: 'center'}}>
            <Animated.View
            style={[style.defaultShadow,{
                padding: 16, 
                borderRadius: 4,
                backgroundColor: '#FFF',
                opacity: opacityValue,
                transform:[{translateY:translateYValue}]
                }]}>
                    <ButtonIcon
                    styleContainer={{position:'absolute', top: 0, right: 0, zIndex:99}}
                    iconName={'close'}
                    iconSize={16}
                    hasShadow={false}
                    onPress={props.onClose} />
                    {props.menuItems}
            </Animated.View>
            </View>
        );
}