import { usernameCS } from "../helpers/Constraints";

function createLoginBody(usernameOrEmail, password){
    let variables = {
        loginModel: {
            emailOrUsername: usernameOrEmail,
            password: password
        }
    };
    let body = {
        query: 'query ($loginModel: LoginModel!){login(loginModel: $loginModel){token email username}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createVerifyAuthenticationBody(){
    let body = {
        query: 'query {verifyAuthentication{success}}'
    };
    return body;
}

function createSearchBody(start, max, fuzzyName, filters, latitude, longitude, distance){
    let variables = {
        locationsModel: {
            paging: {
                start: start,
                max: max
            },
            latitude: latitude,
            longitude: longitude,
            distance: distance
        }
    };

    if(fuzzyName !== null && fuzzyName !== ''){
        variables.locationsModel['fuzzyFilter'] = {
            value: fuzzyName,
            filter: 'NAME'
        };
    } 

    if(filters !== null){
        variables.locationsModel['filters'] = filters;
    }

    let body = {
        query: 'query ($locationsModel: LocationsInputModel!){getLocationNearByElastic(locationsModel: $locationsModel) {paging{ start stop } locations{ id name streetAddress city state zipCode latitude longitude totalRatings wifiRating outletsRating noiseRating seatingRating imageUri distance }}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createGetReviewByLocationBody(start, max, locationId){
    let variables = {
        reviewsInputModel: {
            paging: {
                start: start,
                max: max
            },
            locationId: locationId
        }
    };
    let body = {
        query: 'query ($reviewsInputModel: ReviewsInputModel!){getReviewsByLocationId(reviewsModel: $reviewsInputModel) {paging{ start stop } reviews{ id userId locationId noise wifi seating outlets title review imageUris dateCreated }}}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createGetLocationAndReviewsByLocationBody(start, max, locationId){
    let variables = {
        reviewsInputModel: {
            paging: {
                start: start,
                max: max
            },
            locationId: locationId
        }
    };
    let body = {
        query: 'query ($reviewsInputModel: ReviewsInputModel!){' + 
            'getReviewsByLocationId(reviewsModel: $reviewsInputModel) {paging{ start stop } reviews{ id userId locationId noise wifi seating outlets title review imageUris dateCreated }}' +
            'getLocationById(id: \"'+ locationId +'\") {id name streetAddress city state zipCode latitude longitude totalRatings wifiRating outletsRating noiseRating seatingRating imageUri distance}' +
            '}',
        variables: JSON.stringify(variables)
    };
    return body;
};

function createIsEmailAvailableBody(email){
    let body = {
        query: 'query {isEmailAvailable(email: \"' + email + '\") {sucess}}'
    };
    return body;
};

function createIsUsernameAvailbleBody(username){
    let body = {
        query: 'query {isUsernameAvailable(email: \"' + username + '\") {sucess}}'
    };
    return body;
};

function creatGetLocationByIdBody(locationId){
    let body = {
        query: 'query {getLocationById(id: \"'+ locationId +'\") {id name streetAddress city state zipCode latitude longitude totalRatings wifiRating outletsRating noiseRating seatingRating imageUri distance}}'
    };
    return body;
}

function createGetReviewsByUserIdBody(start, max, userId){
    let variables = {
        userReviewsInputModel: {
            paging: {
                start: start,
                max: max
            },
            userId: userId
        }
    };
    let body = {
        query: 'query ($userReviewsInputModel: UserReviewsInputModel!){getReviewsByUserId(userReviewsModel: $userReviewsInputModel) {paging{ start stop } reviews{ id userId locationId noise wifi seating outlets title review imageUris dateCreated }}}',
        variables: JSON.stringify(variables)
    };
    return body;
}

export {createLoginBody, createIsEmailAvailableBody, createIsUsernameAvailbleBody, createVerifyAuthenticationBody, createSearchBody, createGetReviewByLocationBody, creatGetLocationByIdBody, createGetReviewsByUserIdBody, createGetLocationAndReviewsByLocationBody};