import React from 'react';
import {Dimensions} from 'react-native';

import {ProfileNavigator} from './ProfileStackNavigator';
import {AddLocationAreaNavigator} from './AddLocationStackNavigator';
import {SearchLocationsArea} from '../areas/SearchLocations';
import {AddLocationArea} from '../areas/AddLocation';
import {ProfileArea} from '../areas/Profile';

import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';
import {createBottomTabNavigator} from '@react-navigation/bottom-tabs';



const Tab = createBottomTabNavigator();

const { height, width } = Dimensions.get('window');

let tabNavMargins;

function choseStyle(width){
    if(width > 1000){
      tabNavMargins = ((width - (width - 200)) / 2)
    } else {
      tabNavMargins = ((width - (width - 30)) / 2);
    }
  }

choseStyle(width);

export function MainNavigator({navigator}){

    const tabBarStyle = {
      position: 'absolute',
      left: tabNavMargins,
      right: tabNavMargins,
      bottom: 0,
      marginBottom: 16,
      border: 'none',
      borderRadius: 4,
      shadowColor: "#333333",
      shadowOffset: {
        width: 0,
        height: 3,
      },
      shadowOpacity: 0.26,
      shadowRadius: 6.62,
      elevation: 8,
      backgroundColor: '#ffffff',};

    return (
        <Tab.Navigator
            name="HomeTabs"
            initialRouteName="Home"
            tabBarOptions={{
              showLabel: false,
              keyboardHidesTabBar: true,
              style: tabBarStyle,
            }}>
            <Tab.Screen name="Profile" options={{
                tabBarIcon: ({focused, color, size }) => (<MaterialCommunityIcons name={focused ? 'account' : 'account-outline'} size={24} color={"#222222"} />)
              }} component={ProfileArea}/>
            <Tab.Screen name="Home" options={{
                tabBarIcon: ({focused, color, size }) => (<MaterialCommunityIcons name={focused ? 'home' : 'home-outline'} size={24} color={"#222222"} />)
              }} component={SearchLocationsArea}/>
            <Tab.Screen name="AddLocation" options={{
                unmountOnBlur: true,
                tabBarIcon: ({focused, color, size }) => (<MaterialCommunityIcons name={focused ? 'plus-circle' : 'plus-circle-outline'} size={24} color={"#222222"} />)
              }} component={AddLocationArea}/>
        </Tab.Navigator>
    );
}