import React, {Component} from 'react';
import {View} from 'react-native';

import {default as TextBody} from '../components/TextBody';
import {ButtonPrimary} from '../components/ButtonPrimary';
import {ButtonCTA} from '../components/ButtonCTA';

export class AddLocationArea extends Component{

    _addLocationForm = () => {
        this.props.navigation.navigate('AddLocationForm');
    };

    render(){
        return (
            <View style={{
            flex: 1,
            backgroundColor: '#F8F7F0',
            alignItems: 'center',
            justifyContent: 'center',
            }}>
                <View style={{
                flexDirection: 'column',
                padding: 16,
                borderRadius: 4,
                alignItems: 'center',
                backgroundColor: '#fff',
                shadowColor: "#000",
                shadowOffset: {
                    width: 0,
                    height: 3,
                },
                shadowOpacity: 0.29,
                shadowRadius: 4.65,
                elevation: 7,
                }}>
                    <TextBody style={{textAlign: 'center'}} text={"Don't see your favorite location? \n Add it here!"} weight={"bold"} fontSize={32}/>
                    <ButtonPrimary 
                    text={"Add Location"}
                    onPress={() => this._addLocationForm()}/>
                    {/* <ButtonCTA
                    iconName={"account"}
                    iconColor={'#FFF'} 
                    text={"Add Location"}
                    hasShadow={false}
                    buttonBackgroundColor={'#222'}
                    onPress={() => this._addLocationForm()}/> */}
                </View>   
            </View>
        );
    };
};
