import React,{useRef, useContext, useEffect, useState} from 'react';
import {Easing, View, StyleSheet, Dimensions} from 'react-native';

import {FormTextInput} from '../components/FormTextInput';
import {TextInputSecure} from '../components/TextInputSecure';
import {ButtonPrimary} from '../components/ButtonPrimary';
import { default as TextBody} from '../components/TextBody';
import {ButtonText} from '../components/ButtonText';
import {ButtonIcon} from '../components/ButtonIcon';
import {NotificationAlert} from '../components/NotificationAlert';
import {MenuToggleable} from '../components/MenuToggleable';
import {MenuToggleableItem} from '../components/MenuToggleableItem';
import {InformationView} from '../components/InformationView';

import {graphql} from '../graphql/graphqlService';
import {createLoginBody} from '../graphql/queries';
import {setUserAuth} from '../graphql/sessionData';
import {AuthContext} from '../context/Authentication';

import style from '../styles/Container.Form.Style';
import {Linking} from 'expo';

function loginFetch(id, password){
    let body = createLoginBody(id, password);
    return graphql(body);
}

const { width, height } = Dimensions.get('window');
const trdWidth = (width/3);


export function LoginArea ({navigation}){

    const [isLoading, setIsLoading] = useState(false);

    const [id, onChangeId] = useState('email or username');
    const [password, onChangePassword] = useState('password');
    const {login} = useContext(AuthContext);
    
    const [menuVisible, menuToggle] = useState(false);
    const [isError, errorToggle] = useState(false);

    useEffect(() => {
        Linking.getInitialURL().then((data) => {
            let { path, queryParams } = Linking.parse(data);
            Linking.set
            if(path.includes('verify') || path.includes('Verify')){ 
                navigation.push('Verify', queryParams);
            } else if(path.includes('reset-password') || path.includes('VerifyResetPassword')){
                navigation.push('VerifyResetPassword', queryParams);
            } else if(path.includes('Register')){
                navigation.push('Register', queryParams);
            }
        });
    }, []);

    return (
        <View style={style.containerBody}>
            <View style={styles.containerTitle}>
                <TextBody text={'FERN '} weight={'black'} fontSize={26}/>
                <TextBody text={'STUDY SPOT FINDER'} />
            </View>
            <View style={style.containerMain}>
                <View style={{width: '100%', flexDirection: 'row', justifyContent: 'space-between', zIndex:100}}>
                    <View style={{width: 32, height: 32}} />
                    <TextBody text={'LOGIN'} weight={'bold'} style={{justifySelf:'center'}}/>
                    <ButtonIcon
                    style={{justifySelf:'end'}}
                    iconName={'dots-vertical'}
                    iconSize={24}
                    onPress={() => {
                        if(!menuVisible){
                            menuToggle(true);
                        } else {
                            menuToggle(false);
                        }
                    }} />
                    {menuVisible &&
                        <MenuToggleable 
                        onClose={() => {menuToggle(false);}}
                        styleContainer={{right:0,top:30}}
                        menuItems={
                            <>
                            <MenuToggleableItem itemDelay={0} itemIcon={"account-check"} itemText={"Verify Account"} onMenuItemPress={() => navigation.push('Verify')}/>
                            <View style={{height:12}}/>
                            <MenuToggleableItem itemDelay={50} itemIcon={"shield-check"} itemText={"Verify Reset Password"} onMenuItemPress={() => navigation.push('VerifyResetPassword')}/>
                            </>
                        }
                        />
                    }
                </View>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Email/Username'} />
                </View>
                <FormTextInput
                    value={id}
                    onChangeText={text => onChangeId(text)}/>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Password'} />
                    <ButtonText
                        text="Forgot your password?"
                        onPress={() => navigation.push('ResetPassword')}/>
                </View>
                <TextInputSecure
                    value={password} 
                    onChangeText={text => onChangePassword(text)}/>
                <View style={styles.containerBtnLogin}>
                    <ButtonPrimary 
                        text="SUBMIT"
                        onPress={() => {
                            setIsLoading(true);
                            loginFetch(id, password).then(
                                (result) =>{
                                    setIsLoading(false);
                                    if(result.error !== null){
                                        errorToggle(true);
                                    }
                                    if(result.data !== null){
                                        setUserAuth(result.data)
                                            .then(() => {login();});
                                    }
                                },
                                (error) => {}
                            );}
                        }/>
                </View>
            </View>
            <View style={styles.containerRegister} >
                <ButtonText
                text={"Make a new account. "}
                fontSize={16}
                onPress={() => navigation.push('Register')}/>
                <ButtonText
                text={"Here."}
                weight={'bold'}
                fontSize={16}
                onPress={() => navigation.push('Register')}/>
            </View>
            {isLoading &&
            <NotificationAlert text={"Logging you in... please wait"}/>
            }
            {isError &&
            <NotificationAlert text={"Failed to Login"} onPress={() => {errorToggle(false)}}/>
            }
        </View>
    );
}

let containerTitleWidth;

function choseStyle(width){
    if(width > 1000){
        containerTitleWidth = trdWidth
    } else {
        containerTitleWidth = width - 40
    }
  }

choseStyle(width);

const styles = StyleSheet.create({
    containerBody: {
        flex: 1,
        backgroundColor: '#F8F7F0',
        alignItems: 'center',
        justifyContent: 'center',
    },
    containerTitle: {
        width: containerTitleWidth,
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'baseline',
    },
    containerLabel: {
        width: '100%',
        flexDirection: 'row',
        justifyContent: 'space-between',
    },
    containerRegister: {
        flexDirection: 'row',
        marginTop: 6,
        marginBottom: 6
    },
    containerBtnLogin: {
        width: '100%',
        flexDirection: 'row-reverse',
        justifyContent: 'space-between',
    },
    containerMenu: {
        width: 20,
        height: 20,
        backgroundColor: '#222'
    },
    labelLogin: {
      fontWeight: '700',
      letterSpacing: 1,  
    },
    labelFern: {
        fontSize: 24,
        fontWeight: '700',
        letterSpacing: 1,  
      },
});