import Constants from 'expo-constants';

const env = 'dev';
const graphqluri = 'https://' + env + '.fernapp.co/graphql';
const imageuri = 'https://' + env + '.fernapp.co/images';

async function graphql(body){
    var headers = {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
    };
    const res = await fetch(graphqluri, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify(body)
    });
    return await res.json();
};

async function graphqlAuth(token, body){
    var headers = {
        'Authorization': 'Bearer ' + token,
        'Content-Type': 'application/json',
        'Accept': 'application/json'
    };
    headers['Authorization'] = 'Bearer ' + token;
    const res = await fetch(graphqluri, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify(body)
    });
    return await res.json();
};

async function imageAuth(token, image, locationId, reviewId){
    var headers = {
        'Authorization': 'Bearer ' + token,
        'location-id': locationId,
        'review-id': reviewId,
        'Accept': 'application/json'
    };
    var formData = new FormData();
    
    if (!Constants.platform.ios && !Constants.platform.android) {
        var imageArray = image.uri.split(',');
        var dataTypeArray = imageArray[0].split(':');
        var imageBlobWeb = new Blob([imageArray[1]], {type: dataTypeArray[1]}); 
        formData.append('file', imageBlobWeb, 'image.jpg');   
    } else {    
        var imageBlobMobile = {
            name: 'image',
            type: 'image/jpeg',
            uri: Constants.platform.android ? image.uri : image.uri.replace("file://", "")
        };
        formData.append('file', imageBlobMobile);
    }
    
    const res = await fetch(imageuri, {
        method: 'POST',
        headers: headers,
        body: formData
    });
    return await res.json();
};


export {graphql, graphqlAuth, imageAuth};