import React from 'react';
import {createStackNavigator} from '@react-navigation/stack';
import {LoginArea} from '../areas/Login';
import {RegisterArea} from '../areas/Register';
import {VerifyArea} from '../areas/Verify';
import {ResetPasswordArea} from '../areas/ResetPassword';
import {VerifyResetPasswordArea} from '../areas/VerifyResetPassword';

const Stack = createStackNavigator();

export function LoginNavigator({navigation}){
    return (
        <Stack.Navigator name="LoginStack" headerMode='none'>
            <Stack.Screen name="Login" component={LoginArea}/>
            <Stack.Screen name="Register" component={RegisterArea}/>
            <Stack.Screen name="Verify" component={VerifyArea}/>
            <Stack.Screen name="ResetPassword" component={ResetPasswordArea}/>
            <Stack.Screen name="VerifyResetPassword" component={VerifyResetPasswordArea}/>
        </Stack.Navigator>
    );
}
    