import React from 'react';
import {View, StyleSheet, TouchableOpacity} from 'react-native'; 
import { default as TextBody} from '../components/TextBody';

export function ButtonPrimary(props) {
        return (
            <TouchableOpacity style={{...props.styleContainer}} onPress={props.onPress}>
                <View style={[{...props.styleButtonContainer, 
                    borderRadius: (props.buttonBorderRadius != null) ? props.buttonBorderRadius : 4,}, 
                    styles.buttonPrimaryContainer]}>
                    <TextBody text={props.text} fontSize={props.fontSize} weight={'semiBold'} color={(props.textColor != null) ? props.textColor : '#FFF'}/>
                </View>
            </TouchableOpacity>
        );
}

const styles =  StyleSheet.create({
    buttonPrimaryContainer: {
        paddingLeft: 10,
        paddingRight: 10,
        paddingTop: 8,
        paddingBottom: 8,
        backgroundColor: '#222',
    },
    buttonIcon: {
        paddingTop: 3,
        paddingRight: 8,
    }
});