import React from 'react';
import { View, StyleSheet, Image, TouchableWithoutFeedback, Dimensions} from 'react-native';

import { setRatingIconColor } from '../helpers/SetRatingIconColor';
import { BarIcon } from '../components/BarIcon';
import { default as TextTitle} from '../components/TextTitle';
import { default as TextBody} from '../components/TextBody';

import { wifiRatingConverter, noiseRatingCoverter, seatingRatingConverter, outletRatingConverter, distanceConverter, resizeLocationName} from '../helpers/Converters';

export function LocationCard (props) {
  const {location} = props;
  const Component = () => (
    <TouchableWithoutFeedback onPress={props.onPress}> 
      <View style={styles.containerCard}>
        <Image source={{uri : location.imageUri}} style={styles.displayImage} resizeMode="cover" />
        <View style={styles.containerDistanceFrom}>
          <TextBody text= {distanceConverter(location.distance, 2) + " mi"} weight={"semiBold"} fontSize={14}/>
        </View>
        <View style={styles.containerLocationInfo}>
          <View style={styles.containerRatings}>
            <BarIcon 
              iconNames={[wifiRatingConverter(location.wifiRating),noiseRatingCoverter(location.noiseRating),seatingRatingConverter(location.seatingRating),outletRatingConverter(location.outletsRating)]}
              iconColors={[setRatingIconColor(location.wifiRating),setRatingIconColor(location.noiseRating),setRatingIconColor(location.seatingRating),setRatingIconColor(location.outletsRating)]}
              iconSize = {24}
            />
          </View>
          <View style={styles.containerDetails}>
            <TextBody text={location.name} weight={'bold'}  fontSize={32}/>
            <View style={styles.containerAddress}>
                <TextBody style={{letterSpacing:0.15}} text={location.streetAddress + " " + location.city + ", " + location.state + " " + location.zipCode} fontSize={16} color={'#777777'}/>
            </View>
          </View>
        </View>
      </View>
    </TouchableWithoutFeedback>
  );
  const card = React.useMemo(Component);
  return (card);
}

const { height, width } = Dimensions.get('window');
const cardSize = Math.min(width, height) * .9;

const styles = StyleSheet.create({
  containerCard: {
    flex: 1,
    flexDirection: 'column',
    alignSelf: 'center',
    alignItems: 'center',
    justifyContent: 'center',
    width: cardSize,
    height: cardSize,
    margin: 8,
    borderRadius: 8,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 3,
    },
    shadowOpacity: 0.29,
    shadowRadius: 4.65,
    elevation: 7,
    backgroundColor: '#000000',
  },
  containerLocationInfo: {
    position: 'absolute',
    flex: 1,
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'flex-end',
    width: cardSize,
    height: cardSize,
    borderRadius: 8,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 3,
    },
    shadowOpacity: 0.29,
    shadowRadius: 4.65,
    elevation: 7,
  },
  containerDetails: {
    width: cardSize-16,
    flexDirection: 'column',
    paddingHorizontal: 12,
    paddingBottom: 12,
    paddingTop: 8,
    marginBottom: 8,
    borderRadius: 4,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.23,
    shadowRadius: 2.62,
    elevation: 4,
    backgroundColor: '#ffffff',
  },
  containerRatings: {
    flexDirection: 'row',
    paddingHorizontal: 8,
    paddingTop: 4,
    paddingBottom: 4,
    marginLeft: 8,
    marginRight: 'auto',
    marginBottom: 4,
    borderRadius: 4,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.23,
    shadowRadius: 2.62,
    elevation: 4,
    backgroundColor: '#ffffff',
  },
  containerDistanceFrom: {
    position: 'absolute',
    top: 8,
    left: 0,
    flex: 1,
    flexDirection: 'row',
    paddingHorizontal: 8,
    paddingTop: 4,
    paddingBottom: 8,
    borderBottomLeftRadius: 0,
    borderBottomRightRadius: 4,
    borderTopLeftRadius: 0,
    borderTopRightRadius: 4,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.23,
    shadowRadius: 2.62,
    elevation: 4,
    backgroundColor: '#ffffff',
  },
  containerAddress: {
    flexDirection: 'column',
    alignItems: 'flex-start'
  },
  rating: {
    flex: 1,
    fontSize: 24,
  },
  ratingSpacers: {
    width: 2,
  },
  displayImage: {
    flex: 1,
    width: cardSize,
    height: cardSize,
    borderRadius: 8,
  }
});
