import React from 'react';
import {View, TouchableOpacity} from 'react-native'; 

import { default as TextBody} from '../components/TextBody';
import { ButtonPrimary } from '../components/ButtonPrimary';

import {MaterialCommunityIcons} from '@expo/vector-icons'; 

export function Dialog(props){

    return (
        <TouchableOpacity style={{position: 'absolute', width:'100%', height:'100%', backgroundColor:'rgba(0, 0, 0, 0.4)'}} onPress={props.onDeny}>
        <View style={{
        ...props.styleContainer,
        position: 'absolute',
        width: 440,
        top: '40%',
        left: '50%',
        marginLeft: -220,
        flexDirection: 'column',        
        padding: 24,
        borderRadius: (props.containerBorderRadius != null) ? props.containerBorderRadius : 8,
        backgroundColor: (props.containerBackgroundColor != null) ? props.containerBackgroundColor : '#FFF',
        justifyContent: 'flex-start',
        shadowColor: "#222222",
        shadowOffset: {
            width: 0,
            height: 3,
        },
        shadowOpacity: props.hasShadow ? 0.29 : 0.0,
        shadowRadius: props.hasShadow ? 4.65 : 0,
        elevation: props.hasShadow ? 4 : 0, 
        }}>
            <View style={{flexDirection:'row'}}>
                <View style={{height: 32, width: 32, marginRight:12, justifyContent: 'center', alignItems:'center', backgroundColor:'rgba(0, 0, 0, 0.2)', borderRadius:1000}}>
                <MaterialCommunityIcons
                name={props.isAlert ? 'alert-circle-outline' : 'information-outline'}
                color={(props.iconColor != null) ? props.iconColor : '#222'}
                size={24}
                />
                </View>
                <TextBody text={props.title} color={(props.titleColor != null) ? props.titleColor : '#222'} weight={(props.titleWeight != null) ? props.titleWeight : 'bold'} fontSize={30}/>
            </View>
            <TextBody text={props.text} style={{marginTop:8}} color={(props.textColor != null) ? props.textColor : '#AAA'} weight={(props.textWeight != null) ? props.textWeight : 'regular'} fontSize={16}/>
            <View style={{flexDirection:'row', marginTop:16, justifyContent:'flex-end'}}>
                <ButtonPrimary text={props.buttonDenyText} onPress={props.onDeny}/>
                <ButtonPrimary text={props.buttonConfirmText} onPress={props.onConfirm} styleContainer={{marginLeft:8}}/>
            </View>
        </View>
        </TouchableOpacity>
    );
}