import React, { useRef, useEffect } from 'react';
import {Animated, Easing, View, Dimensions, TouchableOpacity} from 'react-native';

import {default as TextBody} from '../components/TextBody';
import { ButtonIcon } from '../components/ButtonIcon';

import {MaterialCommunityIcons} from '@expo/vector-icons'; 


export const NotificationAlert = (props) => {
    
    const translateYValue = useRef(new Animated.Value(30)).current;
    const opacityValue = useRef(new Animated.Value(0)).current;
    const alertVisible = props.alertVisible; 

    React.useEffect(() => {
        Animated.timing(translateYValue, {
            toValue: (props.translateBy != null) ? -props.translateBy : -38,
            easing: Easing.elastic(1),
            duration: 1000
        }).start();
        // const exit = setTimeout(() => {
        //     Animated.timing(translateYValue, {
        //         toValue: 38,
        //         easing: Easing.elastic(1),
        //         duration: 1000
        //     }).start();
        // }, 3200);
        return () => {
            // clearTimeout(exit);
        }
      }, []);

    return (
        <Animated.View style={{
        ...props.styleContainer,
        flexDirection: 'row',
        position: 'absolute',
        left: '50%',
        bottom: (props.alertHeight != null) ? -props.alertHeight : -30 , //-30
        width: (props.alertWidth != null) ? props.alertWidth : 320,
        height: (props.alertHeight != null) ? props.alertHeight : 'auto',
        marginLeft: (props.alertWidth != null) ? -props.alertWidth : -160,
        paddingTop: 6,
        paddingBottom: 6,
        paddingLeft: 12,
        paddingRight: 12,
        justifyContent: 'space-between',
        alignItems: 'center',
        borderRadius: (props.borderRadius != null) ? props.buttonBorderRadius : 2,
        backgroundColor: '#222',
        zIndex: 199, 
        transform: [{ translateY: translateYValue }] //translateYValue
        }}>
            <TextBody text={props.text} fontSize={12} weight={"semiBold"} color={(props.textColor != null) ? props.textColor : '#fff'} />
            <View style={{width:8}}/>
            <ButtonIcon iconName={'close'} iconColor={'#ffffff'} hasShadow={false} iconSize={16} buttonSize={16} onPress={props.onPress} />
        </Animated.View>
    );
}