import React from 'react';
import {View, StyleSheet} from 'react-native'; 

import {BarIconItem} from './BarIconItem';

import MaterialCommunityIcons from 'react-native-vector-icons/MaterialCommunityIcons';

export class BarIcon extends React.Component {

    constructor(props) {
        super(props)
    }

    static defaultProps = {
        iconLabels: [null,null,null,null]     
    }

    _createBarIconItem = (position , itemProps, hasSpacer) => {
        return(
            <>
            <BarIconItem
                iconName={itemProps.iconNames[position]}
                iconSize={itemProps.iconSize}
                iconColor={itemProps.iconColors[position]}
                iconLabel={itemProps.iconLabels[position]}  
                labelColor={itemProps.labelColor}
                iconBorderWidth={itemProps.iconBorderWidth}
                iconBorderRadius={itemProps.iconBorderRadius}
                iconBorderColor={itemProps.iconBorderColor}
                containerOrientation={itemProps.containerOrientation} 
                alignItems={itemProps.alignLabel} />
            {hasSpacer && <View style={styles.ratingSpacers} />}
            </>
        )
    }
    
    _createBarIconItemGroup = (itemProps) => {
        var itemGroup = [];
        for (let i = 0; i < 4; i++) {
            var item = () => this._createBarIconItem(i,itemProps);
            itemGroup.push({item:item});
        }
        console.log(itemGroup);
        return itemGroup;
    };

    render(){
        const {iconNames, iconColors, iconLabels, iconBorderColor, iconBorderWidth, iconBorderRadius, iconSize, containerOrientation, alignLabel, labelColor} = this.props;
        const barIconProps = {iconNames, iconColors, iconLabels, iconBorderColor, iconBorderWidth, iconBorderRadius, iconSize, containerOrientation, alignLabel, labelColor};
        // const barIconItems = this._createBarIconItemGroup(barIconProps);
        return (
            <View style={styles.containerRatings}>
                {this._createBarIconItem(0,barIconProps,true)}
                {this._createBarIconItem(1,barIconProps, true)}
                {this._createBarIconItem(2,barIconProps, true)}
                {this._createBarIconItem(3,barIconProps, false)}
            </View>
        )
    }
}


const styles =  StyleSheet.create({
    containerRatings: {
        flexDirection: 'row',
        justifyContent: 'flex-start',
    },
    ratingSpacers: {
        width:6
    }
});