import React from 'react';
import {View} from 'react-native'; 

import { default as TextBody} from '../components/TextBody';
import {MaterialCommunityIcons} from '@expo/vector-icons'; 

export function InformationView(props){

    return (
        <View style={{
        ...props.styleContainer,
        width: '100%',
        flexDirection: 'row',        
        padding: 12,
        borderRadius: (props.containerBorderRadius != null) ? props.containerBorderRadius : 8,
        backgroundColor: (props.containerBackgroundColor != null) ? props.containerBackgroundColor : '#222',
        justifyContent: 'flex-start',
        alignItems: 'center',
        shadowColor: "#222222",
        shadowOffset: {
            width: 0,
            height: 3,
        },
        shadowOpacity: props.hasShadow ? 0.29 : 0.0,
        shadowRadius: props.hasShadow ? 4.65 : 0,
        elevation: props.hasShadow ? 4 : 0, 
        }}>
            <View style={{height: 32, width: 32, marginRight:12, justifyContent: 'center', alignItems:'center', backgroundColor:'rgba(255, 255, 255, 0.5)', borderRadius:1000}}>
            <MaterialCommunityIcons
            name={props.isAlert ? 'alert-circle-outline' : 'information-outline'}
            color={(props.iconColor != null) ? props.iconColor : '#222'}
            size={24}
            />
            </View>
            <TextBody text={props.text} color={(props.textColor != null) ? props.textColor : '#FFF'} weight={(props.textWeight != null) ? props.textWeight : 'regular'}/>
        </View>
    );
}