import React from 'react';
import {View, Alert} from 'react-native';

import {FormTextInput} from '../components/FormTextInput';
import {ButtonPrimary} from '../components/ButtonPrimary';
import { default as TextBody} from '../components/TextBody';
import {ButtonTab} from '../components/ButtonTab';
import {InformationView} from '../components/InformationView';

import {graphql} from '../graphql/graphqlService';
import {createVerificationBody} from '../graphql/mutations';
import {emailCS, notEmptyCS} from '../helpers/Constraints';

import style from '../styles/Container.Form.Style';


function verify(email, verificationCode, navigation){
  let body = createVerificationBody(email, verificationCode);
  graphql(body).then(
      (result) => {
          if(result.data !== null){
            navigation.push('Login');
          }
      },
      (error) => {
          Alert.alert(error)
      }
  );
}

export function VerifyArea({navigation, route}) {
  let params = route.params;
  const [email, onChangeEmail] = React.useState(params === undefined ? '' : params.userEmail);
  const [verificationCode, onChangeverificationCode] = React.useState(params === undefined ? '' : params.verificationCode);
  
  return (
      <View style={style.containerBody}>
        <ButtonTab
        styleContainer={{position:'absolute', top:20, left:0}}
        iconName={"arrow-left-bold"}
        buttonBackgroundColor={'#FFF'}
        hasShadow={true}
        leftTab={true}
        onPress={() => navigation.goBack()}/>
        <View style={style.containerMain}>
          <View style={style.containerTextInputLabels}>
            <TextBody text={'Email/Username'} />
          </View>            
          <FormTextInput 
              value={email}
              defaultValue={email}
              onChangeText={text => onChangeEmail(text)}
              constraints={emailCS}/>
          <View style={style.containerTextInputLabels}>
              <TextBody text={'Verification Code'} />
          </View>                 
          <FormTextInput 
              value={verificationCode}
              defaultValue={verificationCode}
              onChangeText={text => onChangeverificationCode(text)}
              constraints={notEmptyCS}/>
          <InformationView styleContainer={{marginVertical:8}} text={'If you haven\'t recieved an email please make sure to check your email\'s spam folder.'} hasShadow={false}/>                       
          <ButtonPrimary
              text="VERIFY"
              onPress={() => verify(email, verificationCode, navigation)}/>
        </View>
      </View>
  );
}
