import React from 'react';
import {View, Alert} from 'react-native';

import {FormTextInput} from '../components/FormTextInput';
import {ButtonPrimary} from '../components/ButtonPrimary';
import { default as TextBody} from '../components/TextBody';
import {ButtonTab} from '../components/ButtonTab';
import {InformationView} from '../components/InformationView';

import {graphql} from '../graphql/graphqlService';
import {emailCS} from '../helpers/Constraints';
import {createPasswordResetEmailBody} from '../graphql/mutations';

import style from '../styles/Container.Form.Style'; 

function sendEmail(email, navigation){
  let body = createPasswordResetEmailBody(email);
  graphql(body).then(
      (result) => {
          if(result.data !== null){
            navigation.push('VerifyResetPassword');
          }
      },
      (error) => {
          Alert.alert(error)
      }
  );
}

export function ResetPasswordArea({navigation}) {
  const [email, onChangeEmail] = React.useState('email');
  return (
      <View style={style.containerBody}>
        <ButtonTab
        styleContainer={{position:'absolute', top:20, left:0}}
        iconName={"arrow-left-bold"}
        buttonBackgroundColor={'#FFF'}
        hasShadow={true}
        leftTab={true}
        onPress={() => navigation.goBack()}/>
        <View style={style.containerMain}> 
          <View style={style.containerTextInputLabels}>
              <TextBody text={'Email/Username'} />
          </View>       
          <FormTextInput 
              value={email}
              onChangeText={text => onChangeEmail(text)}
              constraints={emailCS}/>
          <InformationView styleContainer={{marginVertical:8}} text={'You will be sent an email containing instructions on how to reset your password.'} hasShadow={false}/>                  
          <ButtonPrimary 
              text="SEND EMAIL"
              onPress={() => sendEmail(email, navigation)}/>
        </View>
      </View>
  );
}