import React,{Component} from 'react';
import {View, StyleSheet, Dimensions, FlatList} from 'react-native';

import {ButtonIcon} from '../components/ButtonIcon';
import {default as TextBody} from '../components/TextBody';
import {ReviewCard} from '../components/ReviewCard';
import {MenuToggleable} from '../components/MenuToggleable';
import {MenuToggleableItem} from '../components/MenuToggleableItem';
import {NotificationAlert} from '../components/NotificationAlert';
import {Dialog} from '../components/Dialog';

import {graphqlAuth} from '../graphql/graphqlService';
import {createGetReviewsByUserIdBody} from '../graphql/queries' 
import {getUserAuth, getUserEmail, getUserId} from '../graphql/sessionData'; 
import {AuthContext} from '../context/Authentication';

const { height, width } = Dimensions.get('window');

let containerMainHorizontalMargins;
let containerMainWidth;

export class ProfileArea extends Component {
    static contextType = AuthContext;

    state = {
        userId: "",
        userEmail: "",
        start: 0,
        data: [],
        max: 10,
        reachedEnd: false,
        menuVisible: false, 
        isError: false,
        confirmLogOut: false,
    };

    componentDidMount(){
        this._populateUserInformation();
    }

    _populateUserInformation = () => {
        getUserEmail().then(
            (result) => {
                this.setState({userEmail: result}); 
            }
        );
        getUserId().then(
            (userIdFromSession) => {
                this.setState({userId: userIdFromSession}, 
                this._getUserReviews());
                
            }
        );
    }

    _getUserReviews = () => {
        if(this.state.reachedEnd){
            return;
        }
        getUserAuth().then((token) => {
            let body = createGetReviewsByUserIdBody(this.state.start, this.state.max, this.state.userId);
            graphqlAuth(token, body).then((result) => {
                if(result.data === null){
                    return;
                }
                let reviewsModel = result.data.getReviewsByUserId;
                let reviews = reviewsModel.reviews;
                let paging = reviewsModel.paging;
                if(paging.stop - paging.start !== this.state.max){
                    this.setState({reachedEnd: true});      
                }
                this.setState({
                    data: [...this.state.data, ...reviews],
                    start: paging.stop
                });
            });
        })
    }
    
    _viewLocation = (locationId) => {
        this.props.navigation.navigate('ViewLocation', {locationId: locationId})
    };

    render(){
        return (
            <View style={{width:width,height:height}}>
                <FlatList
                ListHeaderComponent={
                    <View>
                        <View style={styles.containerMain}>
                            <>
                            <ButtonIcon
                            styleContainer={{position:'absolute', top: 16, right: 16}}
                            iconName={'dots-vertical'}
                            iconSize={24}
                            hasShadow={false}
                            onPress={() => {
                                if(!this.state.menuVisible){
                                    this.setState({menuVisible: true});
                                } else {
                                    this.setState({menuVisible: false});
                                }
                            }} />
                            </>
                            <View style={{flexDirection:'row'}}>
                            <TextBody text={"Hello, "} weight={'bold'} fontSize={30}/>
                            <TextBody text={this.state.userId} weight={'semiBold'} fontSize={30} color={'#59806a'}/>
                            </View>
                            <TextBody text={this.state.userEmail} fontSize={16} color={'#BBB'}/>
                        </View>
                        <View style={{marginTop:16,marginLeft:containerMainHorizontalMargins}}>
                                <TextBody text={"MY REVIEWS"} weight={'semiBold'} fontSize={16}/>
                        </View>
                    </View>
                }
                style = {styles.containerFlatList}
                data={this.state.data}
                keyExtractor={item => item.id}
                renderItem={({ item }) => 
                    <ReviewCard review={item} margin={containerMainHorizontalMargins} onPress={() => this._viewLocation(item.locationId)}/>
                }
                onEndReached={() => {this._getUserReviews();}}
                onEndReachedThreshold={50}
                initialNumToRender={5}
                ListFooterComponent={ <View style={styles.listFooter} />}
                />
                {this.state.isError &&
                    <NotificationAlert translateBy={130} text={"No about page, yet."} onPress={() => {this.setState({isError: false})}}/>
                }
                {this.state.menuVisible &&
                    <MenuToggleable 
                    onClose={() => {this.setState({menuVisible: false})}}
                    styleContainer={{right:containerMainHorizontalMargins+8, top: 76}}
                    menuItems={
                        <>
                        <MenuToggleableItem itemDelay={0} itemIcon={"settings"} itemText={"Options"} onMenuItemPress={() => {}}/>
                        <View style={{height:12}}/>
                        <MenuToggleableItem itemDelay={50} itemIcon={"information-outline"} itemText={"About"} onMenuItemPress={() => {}}/>
                        <View style={{height:12}}/>
                        <MenuToggleableItem itemDelay={100} itemIcon={"logout-variant"} itemText={"Logout"} onMenuItemPress={() => {this.setState({confirmLogOut:true})}}/>
                        </>
                    }
                    />
                }
                {this.state.confirmLogOut &&
                    <Dialog 
                    title={"Log out?"} 
                    text={"Are you sure you want to log out?"} 
                    buttonConfirmText={"Log out"} 
                    buttonDenyText={"Cancel"}
                    onConfirm={() => {this.context.logout()}}
                    onDeny={() => {this.setState({confirmLogOut:false})}} 
                    hasShadow={true}
                    />
                }
            </View>
        );
    }
}

function choseStyle(width){
    if(width > 1000){
        containerMainHorizontalMargins = ((width - (width - 200)) / 2)
    } else {
        containerMainHorizontalMargins = ((width - (width - 30)) / 2)
    }
    containerMainWidth = (width - (containerMainHorizontalMargins));
  }

choseStyle(width);

const styles = StyleSheet.create({
    containerMain: {
        borderRadius: 4,
        marginLeft: containerMainHorizontalMargins,
        marginRight: containerMainHorizontalMargins,
        marginTop: 20,
        padding: 16,
        backgroundColor: '#FFF',
        shadowColor: "#333333",
        shadowOffset: {
          width: 0,
          height: 3,
        },
        shadowOpacity: 0.26,
        shadowRadius: 6.62,
        elevation: 6,
    },    
    containerFlatList: {
        flexDirection: 'column',
        width: width,
        height: height,
        alignSelf: 'center',
        backgroundColor: '#F8F7F0',
    }, listFooter: {
        height: 150
    },
});

