import React from 'react';
import {View, StyleSheet} from 'react-native';

import {ButtonPrimary} from './ButtonPrimary';
import {ButtonText} from './ButtonText';
import {ButtonIcon} from './ButtonIcon';
import {FormTextInput} from './FormTextInput';
import {RatingButtonGroup} from '../components/RatingButtonGroup';
import {default as TextBody} from '../components/TextBody';

import {wifiFilterIcons, wifiFilterValues, noiseFilterIcons, noiseFilterValues, seatingFilterIcons, seatingFilterValues, outletsFilterIcons, outletsFilterValues} from '../icons/RatingIcons'; 

export class ElementFilterPlate extends React.Component {
    constructor(){
        super();
        this.state = {
            wifi: this._createSelection('GTE', 'WIFI', null),
            noise: this._createSelection('LTE', 'NOISE', null),
            seating: this._createSelection('GTE', 'SEATING', null),
            outlets: this._createSelection('GTE', 'OUTLETS', null)
        };
    }

    _onChange = () => {
        var filters = [];
        if(this.state.wifi.value !== null){
            filters.push({...this.state.wifi})
        }
        if(this.state.noise.value !== null){
            filters.push({...this.state.noise})
        }
        if(this.state.seating.value !== null){
            filters.push({...this.state.seating})
        }
        if(this.state.outlets.value !== null){
            filters.push({...this.state.outlets})
        }
        this.props.onChange(filters);
    };

    _setWifi = (value) => {
        this.setState({
            wifi: this._createSelection('GTE', 'WIFI', value)
        }, this._onChange);
    };

    _setNoise = (value) => {
        this.setState({
            noise: this._createSelection('LTE', 'NOISE', value)
        }, this._onChange);
    };
    
    _setSeating = (value) => {
        this.setState({
            seating: this._createSelection('GTE', 'SEATING', value)
        }, this._onChange);
    };
    
    _setOutlets = (value) => {
        this.setState({
            outlets: this._createSelection('GTE', 'OUTLETS', value)
        }, this._onChange);
    };

    _createSelection = (operation, filter, value) => {
        return {
            operation: operation,
            filter: filter,
            value: value
        };
    };

    render() {
         return (
            <View style={{flexDirection:'row', justifyContent:'center'}}>
                <View style={{width:32, height:1}}/>
                <View style={{flexDirection:'column'}}>
                    <View style={[styles.containerPlate, {...this.props.styleContainer, width: (this.props.width != null) ? this.props.width : 500, marginBottom:16}]}>
                        <TextBody style={{marginBottom:16}} text={'Search For'} weight={'bold'} fontSize={30}/>
                        <FormTextInput
                            onChangeText={this.props.onChangeText}
                        />
                        {/* <ButtonText 
                        styleContainer={{position:'absolute', top:16, right: 16}} 
                        text={'Clear'} 
                        fontSize={16} 
                        textColor={'#E14B1C'}
                        /> */}
                    </View>
                    <View style={[styles.containerPlate, {...this.props.styleContainer, width: (this.props.width != null) ? this.props.width : 500,}]}>
                        <TextBody style={{marginBottom:16}} text={'Filter For'} weight={'bold'} fontSize={30} />
                        <TextBody stylesheet={styles.containerTextLabel} text={'\\\\ WIFI'} weight={'semiBold'} fontSize={12} />
                        <RatingButtonGroup
                        icons={wifiFilterIcons}
                        ratings={wifiFilterValues}
                        containerWidth={'100%'}
                        onPress={this._setWifi}
                        />
                        <TextBody stylesheet={styles.containerTextLabel} text={'\\\\ NOISE'} weight={'semiBold'} fontSize={12} />
                        <RatingButtonGroup 
                        icons={noiseFilterIcons}
                        ratings={noiseFilterValues}
                        containerWidth={'100%'}
                        onPress={this._setNoise}
                        />
                        <TextBody stylesheet={styles.containerTextLabel} text={'\\\\ SEATING'} weight={'semiBold'} fontSize={12} />
                        <RatingButtonGroup 
                        icons={seatingFilterIcons}
                        ratings={seatingFilterValues}
                        containerWidth={'100%'}
                        onPress={this._setSeating}
                        />
                        <TextBody stylesheet={styles.containerTextLabel} text={'\\\\ OUTLETS'} weight={'semiBold'} fontSize={12} />
                        <RatingButtonGroup 
                        icons={outletsFilterIcons}
                        ratings={outletsFilterValues}
                        containerWidth={'100%'}
                        onPress={this._setOutlets}
                        />
                        <View style={styles.containerBtn}>
                            <ButtonPrimary text='Search' onPress={this.props.onPress}/>
                        </View>
                        {/* <ButtonText 
                        styleContainer={{position:'absolute', top:16, right: 16}} 
                        text={'Clear'} 
                        fontSize={16} 
                        textColor={'#E14B1C'}
                        /> */}
                    </View>
                </View>
                <ButtonIcon
                iconName={'close'}
                iconColor={'#FFF'}
                onPress={this.props.onClose}/>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    containerPlate: {
        flexDirection: 'column',
        borderRadius: 8,
        alignItems: 'flex-start',
        padding: 24,
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 2,
        },
        shadowOpacity: 0.23,
        shadowRadius: 2.62,
        elevation: 4,
        backgroundColor: 'rgb(255, 255, 255)',
    },
    containerTextLabel:{
        marginTop:12,
        marginBottom:4
    }, 
    containerBtn: {
        width: '100%',
        flexDirection: 'row-reverse',
        marginTop: 12,
    },
});