import React,{useState} from 'react';
import {ScrollView, View, StyleSheet} from 'react-native';

import {default as TextBody} from '../components/TextBody';
import {FormTextInput} from '../components/FormTextInput';
import {ButtonPrimary} from '../components/ButtonPrimary';
import {ButtonTab} from '../components/ButtonTab';
import {AutocompleteTextInput} from '../components/AutocompleteTextInput';
import {NotificationAlert} from '../components/NotificationAlert';
import {Dialog} from '../components/Dialog';

import {getUserAuth} from '../graphql/sessionData';
import {graphqlAuth} from '../graphql/graphqlService';
import {createAddLocationBody} from '../graphql/mutations';

import style from '../styles/Container.Form.Style';
import {notEmptyCS, zipcodeCS} from '../helpers/Constraints';

function addLocation(name, streetAdress, city, state, zipCode, navigation){

    let body = createAddLocationBody(name, streetAdress, city, state, zipCode);
    getUserAuth().then((token) => {graphqlAuth(token, body).then(
        (result) => {
            if(result.data !== null){
                navigation.navigate('SearchLocations');
            }
        },
        (error) => {
        }
    );});
};

export function AddLocationFormArea ({navigation}){
    const [name, onChangeName] = React.useState('name');
    const [streetAdress, onChangeStreetAdress] = React.useState('streetAddress');
    const [city, onChangeCity] = React.useState('city');
    const [state, onChangeState] = React.useState('state');
    const [zipCode, onChangeZipCodes] = React.useState('zipCode');
    const [isLoading, setIsLoading] = React.useState(false);
    const [confirmGoBack, setConfirmGoBack] = React.useState(false);

    return (
        <View style={{height: '100%', backgroundColor: '#F8F7F0'}}>
            <View style={[style.defaultShadow,styles.containerMain,{width:500}]}>
                <TextBody text={'Add Location'} style={{marginBottom:12, letterSpacing: .3}} weight={"bold"} fontSize={30}/>
                <TextBody text={'NAME'} stylesheet={styles.containerLabel} weight={"semiBold"} fontSize={12}/>
                <FormTextInput 
                    value={name}
                    onChangeText={text => onChangeName(text)}
                    constraints={notEmptyCS}/>
                <TextBody text={'STREET ADDRESS'} stylesheet={styles.containerLabel} weight={"semiBold"} fontSize={12}/>    
                <FormTextInput 
                    value={streetAdress}
                    onChangeText={text => onChangeStreetAdress(text)}
                    constraints={notEmptyCS}/>
                <TextBody text={'CITY'} stylesheet={styles.containerLabel} weight={"semiBold"} fontSize={12}/>
                <FormTextInput 
                    value={city}
                    onChangeText={text => onChangeCity(text)}
                    constraints={notEmptyCS}/>
                <TextBody text={'STATE'} stylesheet={styles.containerLabel} weight={"semiBold"} fontSize={12}/>
                <AutocompleteTextInput
                    value={state}
                    onChangeText={text => onChangeState(text)}
                    data={states}
                />
                <TextBody text={'ZIPCODE'} stylesheet={styles.containerLabel} weight={"semiBold"} fontSize={12}/>
                <FormTextInput 
                    value={zipCode}
                    onChangeText={text => onChangeZipCodes(text)}
                    constraints={zipcodeCS}/>
                <ButtonPrimary 
                    text="Submit"
                    onPress={() => addLocation(name, streetAdress, city, state, zipCode, navigation)}/>                  
            </View>
            <ButtonTab
            styleContainer={{position:'absolute', top: 20, left: 0}}
            iconName={"arrow-left-bold"}
            buttonBackgroundColor={'#FFF'}
            hasShadow={true}
            leftTab={true}
            onPress={() => {setConfirmGoBack(true)}}/>
            {isLoading &&
                <NotificationAlert text={"Making a submission... please wait"}/>
            }
            {confirmGoBack &&
            <Dialog 
            title={"Dismiss this submission?"} 
            text={"Going back will discard this submission. Are you sure you want to dismiss this submission and go back?"} 
            buttonConfirmText={"Go Back"} 
            buttonDenyText={"Cancel"}
            onConfirm={() => {navigation.goBack()}}
            onDeny={() => {setConfirmGoBack(false)}} 
            hasShadow={true}
            />
            }      
        </View>
    );
};

const states = ["Alabama", "Alaska", "Arizona", "Arkansas", "California", "Colorado", "Connecticut", "Delaware", "District Of Columbia", "Florida", "Georgia", "Hawaii", "Idaho", "Illinois", "Indiana", "Iowa", "Kansas", "Kentucky", "Louisiana", "Maine", "Maryland", "Massachusetts", "Michigan", "Minnesota", "Mississippi", "Missouri", "Montana", "Nebraska", "Nevada", "New Hampshire", "New Jersey", "New Mexico", "New York", "North Carolina", "North Dakota", "Ohio", "Oklahoma", "Oregon", "Pennsylvania", "Rhode Island", "South Carolina", "South Dakota", "Tennessee", "Texas", "Utah", "Vermont", "Virginia", "Washington", "West Virginia", "Wisconsin", "Wyoming"];

const styles = StyleSheet.create({
    containerMain:{
        alignSelf:'center', 
        marginTop:60,
        padding: 24,
        borderRadius: 4,
        backgroundColor:'#FFF'
    },
    containerLabel:{
        marginBottom:4
    }
});