import React from 'react';
import {View, Alert, CheckBox} from 'react-native';

import {FormTextInput} from '../components/FormTextInput';
import {TextInputSecure} from '../components/TextInputSecure';
import {ButtonPrimary} from '../components/ButtonPrimary';
import {ButtonTab} from '../components/ButtonTab';
import { default as TextBody} from '../components/TextBody';
import { NotificationAlert } from '../components/NotificationAlert';

import {graphql} from '../graphql/graphqlService';
import {createRegistrationBody} from '../graphql/mutations';
import {emailCS, passwordCS, usernameCS} from '../helpers/Constraints'

import style from '../styles/Container.Form.Style'; 

function register(isSelected, email, username, password, navigation){
    if(isSelected){
        let body = createRegistrationBody(email, username, password, true);
        graphql(body).then(
            (result) => {
                if(result.data !== null){
                    navigation.push('Verify');
                }
            },
            (error) => {
                Alert.alert(error)
            }
        );
    } else {

    }
}

export function RegisterArea ({navigation}){
    const [email, onChangeEmail] = React.useState('email');
    const [username, onChangeUsername] = React.useState('username');
    const [password, onChangePassword] = React.useState('password');
    const [isSelected, setSelection] = React.useState(false);
    const [checkboxError, checkboxErrorToggle] = React.useState(false);
    const [accountCreationError, accountCreationErrorToggle] = React.useState(false);

    return (
        <View style={style.containerBody}>
            <ButtonTab
            styleContainer={{position:'absolute', top: 20, left: 0}}
            iconName={"arrow-left-bold"}
            leftTab={true}
            hasShadow={true}
            buttonBackgroundColor={'#FFF'}
            onPress={() => navigation.goBack()}/>
            <View style={style.containerMain}>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Email'} />
                </View>
                <FormTextInput 
                    value={email}
                    onChangeText={text => onChangeEmail(text)}
                    constraints={emailCS}/>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Username'} />
                </View>
                <FormTextInput 
                    value={username} 
                    onChangeText={text => onChangeUsername(text)}
                    constraints={usernameCS}/>
                <View style={style.containerTextInputLabels}>
                    <TextBody text={'Password'} />
                </View>
                <TextInputSecure 
                    value={password}
                    onChangeText={text => onChangePassword(text)}
                    constraints={passwordCS}/>
                <View style={{flexDirection: 'row'}}>
                    <View style={{marginTop:4}}>
                    <CheckBox
                    value={isSelected}
                    onValueChange={setSelection}/>
                    </View>
                    <View style={{width:8}} />
                    <TextBody text={'I have read and agree to the terms and conditions and privacy policy.'}/>
                </View>                
                <ButtonPrimary
                    styleContainer={{marginTop:16}} 
                    text="REGISTER"
                    onPress={() => {    
                        if(isSelected){
                        let body = createRegistrationBody(email, username, password, true);
                        graphql(body).then(
                            (result) => {
                                if(result.error !== null){
                                    accountCreationErrorToggle(true);
                                }
                                if(result.data !== null){
                                    navigation.push('Verify');
                                }
                            },
                            (error) => {
                                Alert.alert(error)
                            }
                        );
                    } else {
                        checkboxErrorToggle(true);
                        setTimeout(() => {
                            errorToggle(false);
                        }, 4000);
                    }
                }}/>
            </View>
            {checkboxError &&
            <NotificationAlert text={'Please read and accept our terms & conditions and privacy policy.'} onPress={() => {checkboxErrorToggle(false)}}/>
            }
            {accountCreationError &&
            <NotificationAlert text={'Failed to create account please try again.'} onPress={() => {accountCreationErrorToggle(false)}}/>
            }
        </View>
    );
}
